<template>
    <hr class="ncfb-separator" />
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-separator',
        methods: {
            value() {
                return null
            }
        }
    })

    Ctrl.ALIAS = 'separator'

    export default Ctrl
</script>



