<template>
    <div class="ncfb-radios ncfb-ctrl" :class="{ 'ncfb-ctrl-error': error }">
        <component :is="cmpLbl" :label="data.label" :help="data.help"></component>
        <div class="ncfb-input-cnt" :class="[layout__]">
            <div v-for="(group, groupIndex) in items__" :class="'ncfb-col ncfb-col-'+(groupIndex+1)">
                <div class="ncfb-radio" v-for="item in group">
                    <ui-radio :name="'rc-'+data.uid" :value="item.value" :label="item.value" :checked="data.default" ref="input"></ui-radio>
                </div>
            </div>
        </div>
        <span class="ncfb-error" v-show="error">{{ error }}</span>
    </div>
</template>

<script>
    import Vue from 'vue'
    import AbstractControl from './../../renderer/AbstractControl'
    import UiRadio from "./../../ui/Radio"
    import { each } from './../../../util/util'
    import UiIcon from "./../../ui/Icon";
    import UiHint from "./../../ui/Hint";

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        components: { UiRadio, UiHint, UiIcon },
        name: 'fb-radios',
        methods: {
            getValue() {
                let value = null

                each(this.$refs.input, (el) => {
                    if (el.$refs['input'].checked) {
                        value = el.value
                    }
                })
                return value
            }
        },
        computed: {
            items__() {
                const
                    me = this,
                    data = me.data,
                    layout = data.layout,
                    colNum = (layout === '1-column' || layout === 'side-by-side') ? 1
                        : (layout === '2-column' ? 2
                            : (layout === '3-column' ? 3 : 1)),
                    arr = [],
                    items = data.items,
                    size = Math.ceil(items.length / colNum);
                let i = 0
                for (; i < items.length; i += size) {
                    arr.push(items.slice(i, i+size))
                }
                return arr
            },
            layout__() {
                return 'ncfb-layout-' + this.data.layout
            }
        }
    })

    Ctrl.ALIAS = 'radio'

    export default Ctrl
</script>

<style scoped>
    .ncfb-radios {
        display: block;
        vertical-align: middle;
        margin-bottom: 16px;
    }
    .ncfb-opt-label {
        font-size: .8em;
        margin-bottom: 20px;
        display: block;
    }

    .ncfb-layout-2-column {
        font-size: 0;
    }
    .ncfb-layout-2-column .ncfb-col {
        display: inline-block;
        width: 50%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-layout-3-column .ncfb-col {
        display: inline-block;
        width: 33%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-layout-side-by-side .ncfb-radio {
        display: inline-block;
        margin-right: 20px;
    }
</style>



