<template>
    <div class="ncfb-ctrl" :class="{ 'ncfb-ctrl-error': error }">
        <component :is="cmpLbl" :label="data.label" :help="data.help"></component>
        <input type="text"
               class="ncfb-input"
               :class="{ 'ncfb-input-error': error }"
               :placeholder="data.placeholder"
               @input="error = false"
               ref="input">
        <span v-if="data.help && $root.form.lblPos === 'left' && $root.form.helpIcon === false" class="ncfb-help">{{ data.help }}</span>
        <span class="ncfb-error" v-show="error">{{ error }}</span>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../../renderer/AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-input-text',
        methods: {
            getValue() {
                return this.$refs.input.value
            }
        },
        mounted() {
            this.$refs.input.value = this.data.default
        }
    })

    Ctrl.ALIAS = 'textInput'
    export default Ctrl
</script>



