<template>
    <div class="ncfb-of-cnt">
        <div class="ncfb-of-n">
            <a :class="{ 'ncfb-of-n-a': tab == 'basic' }" @click="tab = 'basic'">Basic data</a>
        </div>
        <div class="ncfb-of-f">
            <div v-show="tab == 'basic'">
                <h2 class="ncfb-of-h">Basic</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Value</label>
                    <input class="ncfb-of-i" :value="data.value" prop="value" @input="vchange($event)" @blur="setDefault($event)" ref="value" />
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Type</label>
                    <ui-select
                            class="ncfb-of-i"
                            items-width="400px"
                            :items="$options.types"
                            id-key="id"
                            name-key="name"
                            :value="data.type"
                            @change="$root.updateCtrl(data.uid, 'type', $event.value.id)">
                        <template slot="items" slot-scope="{ item }">
                            <div class="ncfb-select-item">
                                <span class="ncfb-select-item-name">{{ item.name }}</span>
                                <em v-if="item.desc" class="ncfb-select-item-desc">{{ item.desc }}</em>
                            </div>
                        </template>
                    </ui-select>
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Align</label>
                    <ui-select
                            class="ncfb-of-i"
                            items-width="400px"
                            :items="$options.alignTypes"
                            id-key="id"
                            name-key="name"
                            :value="data.align"
                            @change="$root.updateCtrl(data.uid, 'align', $event.value.id)">
                        <template slot="items" slot-scope="{ item }">
                            <div class="ncfb-select-item">
                                <span class="ncfb-select-item-name">{{ item.name }}</span>
                                <em v-if="item.desc" class="ncfb-select-item-desc">{{ item.desc }}</em>
                            </div>
                        </template>
                    </ui-select>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * Properties editor for controls type of "paragraph".
     */
    import AbstractProps from './../AbstractProps'
    import FbIcon from './../../ui/Icon'
    import UiSelect from './../../ui/Select'

    export default {
        extends: AbstractProps,
        name: 'fb-heading-props',
        components: { FbIcon, UiSelect },
        types: [
            {
                id: 'heading.heading',
                name: 'Heading 1',
                desc: 'Set h1 tag'
            },
            {
                id: 'heading.heading2',
                name: 'Heading 2',
                desc: 'Set h2 tag'
            },
            {
                id: 'heading.heading3',
                name: 'Heading 3',
                desc: 'Set h3 tag'
            },
            {
                id: 'heading.heading4',
                name: 'Heading 4',
                desc: 'Set h4 tag'
            }
        ],
        alignTypes: [
            {
                id: 'left',
                name: 'Left',
                desc: 'Align the heading to the left.'
            },
            {
                id: 'center',
                name: 'Center',
                desc: 'Align the heading to the center.'
            },
            {
                id: 'right',
                name: 'Right',
                desc: 'Align the heading to the right.'
            },
        ],
    }
</script>

<style scoped>
    @import './../../../css/options-form.css';

    .ncfb-of-desc span {
        padding-left: 0;
        width: auto;
        display: inline-block;
    }

    .ncfb-select-item {
        display: block;
        border-bottom: 1px solid #ececec;
        transition: background-color 100ms ease-out;
        cursor: pointer;
        padding-bottom: 13px;
        background: #fafbfc;
        box-shadow: 0 0 33px 0px rgba(0, 0, 0, .1);
    }
    .ncfb-select-item:hover {
        background-color: #e7f2f5;
    }
    .ncfb-select-item:hover em {
    }
    .ncfb-select-item-name {
        padding: 10px 20px;
        font-weight: bold;
        display: block;
        background: #fff;
        margin-bottom: 7px;
    }
    .ncfb-select-item-desc {
        color: gray;
        padding: 10px 20px 6px 20px;
        display: block;
    }
</style>



