<template>
    <div class="ncfb-checkbox">
        <input type="checkbox" :id="'cbx-'+id_" style="display:none" class="ncfb-input" v-model="value">
        <label :for="'cbx-'+id_" class="ncfb-label"><span></span></label>
    </div>
</template>

<script>
    let ids = 0;

    export default {
        name: 'ui-checkbox',
        props: {
            id: {
                type: String,
                default: null
            },
            checked: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                id_: ''
            }
        },
        created() {
            this.id_ = (this.id === null ? ids++ : this.id)
        },
        computed: {
            value: {
                get() { return this.checked },
                set(value) {
                    this.$emit('change', { cmp: this, value: value, target: this.$el })
                }
            }
        },
        watch: {
          id(newVal, oldVal) {
              this.id_ = newVal
          }
        }
    }
</script>

<style scoped>
    .ncfb-checkbox {
        display: inline-block;
        vertical-align: middle;
    }
    .ncfb-label {
        position: relative;
        display: block;
        width: 40px;
        height: 20px;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
        transform: translate3d(0,0,0);
    }
    .ncfb-label:before {
        content: "";
        position: relative;
        top: 3px;
        left: 3px;
        width: 34px;
        height: 14px;
        display: block;
        background: #9a9999;
        border-radius: 8px;
        transition: background .2s ease;
    }
    .ncfb-label span {
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        display: block;
        background: white;
        border-radius: 10px;
        box-shadow: 0 3px 8px rgba(60, 60, 60, .5);
        transition: all .2s ease;
    }
    .ncfb-label span:before {
        content: "";
        position: absolute;
        display: block;
        margin: -18px;
        width: 56px;
        height: 56px;
        background: rgba(31, 18, 86, .5);
        border-radius: 50%;
        transform: scale(0);
        opacity: 1;
        pointer-events: none;
    }
    .ncfb-input:checked + .ncfb-label:before {
        background: #947ADA;
    }
    .ncfb-input:checked + .ncfb-label span {
        background: #4F2EDC;
        transform: translateX(20px);
        transition: all .2s cubic-bezier(.8,.4,.3,1.25), background .15s ease;
        box-shadow: 0 3px 8px rgba(31, 18, 86, .2);
    }
    .ncfb-input:checked + .ncfb-label span:before {
        transform: scale(1);
        opacity: 0;
        transition: all .4s ease;
    }
</style>



