<template>
    <ncfs-modal :opened="opened" @update:opened="$emit('update:opened', $event)" :title="heading__">
        <template #description>
            <div v-html="commonDesc__"></div>
        </template>
        <template #body>
            <div class="ncfb-body">
                <div class="ncfb-controls">
                    <div v-for="(ctrls, group) in controls"
                         :key="group">
                        <h2 v-if="control == null" class="ncfb-ctrl-group">{{ group }}</h2>
                        <div v-if="backBtn" class="ncfb-control" @click="back">
                            <div><fb-icon name="back"></fb-icon></div>
                            <span>back</span>
                        </div>
                        <div v-for="(ctrl, key) in ctrls"
                             :key="key"
                             class="ncfb-control"
                             @click="ctrlClick(key)"
                             @mouseover="showInfo(key)">
                            <div v-html="ctrl.info.icon"></div>
                            <span>{{ ctrl.info.name }}</span>
                        </div>
                    </div>
                </div>
                <div class="ncfb-info">
                    <div v-if="info">
                        <h1>{{ info.title }}</h1>
                        <img v-if="info.preview" src="info.image">
                        <div class="ncfb-desc" v-html="info.desc"></div>
                    </div>
                </div>
            </div>
        </template>
    </ncfs-modal>
</template>

<script>
    import Vue from 'vue'
    import FbIcon from './ui/Icon'
    import $ from 'cash-dom'
    import { clone, each } from './../util/util'

    export default Vue.component('ncfs-ctrl-selector', {
        components: { FbIcon },
        props: {
            opened: {
                type: Boolean,
                default: false
            },
            target: {
                type: Number,
                default: null
            },
            insertMode: {
                type: String,
                default: 'after'
            }
        },
        data() {
            return {
                options: {},
                controls: {},
                backBtn: false,
                info: null,
                control: null
            }
        },
        methods: {
            /**
             * @param object controls Controls to group by "group" key.
             * @method groupControls
             * @return plainObject
             */
            groupControls(controls) {
                const ctrls = {}
                each(controls, (value, key) => {
                    const k = value.info.group || 'misc'
                    if (!ctrls[k]) {
                        ctrls[k] = {}
                    }
                    ctrls[k][key] = value
                })
                return ctrls
            },
            /**
             * Handle click on control.
             * Depending if control have subtypes displays subtypes or adds control to
             * builder.
             *
             * @method
             * @param str alias Alias of the control.
             */
            ctrlClick(alias) {
                const
                    me = this,
                    $root = me.$root;

                if (alias.indexOf('.root') !== -1) {
                    alias = alias.split('.')[0]
                    me.controls = this.groupControls($root.getCtrlSubtypesDef(alias))
                    me.backBtn = true
                    me.control = alias
                } else {
                    $root.addCtrl(me.target, alias, me.insertMode, me.options)
                    // Close modal.
                    me.$emit('update:opened', false)
                }
            },
            back() {
                const me = this;
                me.controls = me.groupControls(me.$root.getCtrlDefs())
                me.backBtn = false
                me.control = null
            },
            showInfo(alias) {
                const
                    me = this,
                    $root = me.$root,
                    def = $root.getCtrlDef(alias),
                    info = def.info;

                if (info) {
                    me.info = {
                        cmp: def.cmp,
                        title: info.title || info.name,
                        desc: info.desc || '',
                        data: clone(info.data)
                    }
                }
            }
        },
        computed: {
            /**
             * Return heading.
             *
             * @return string
             * @private
             */
            heading__() {
                if (!this.backBtn) {
                    return 'Fields'
                }

                return this.$root.getCtrlDef(this.control+'.root').info.name
            },
            /**
             * Return controls common description.
             *
             * @return string
             * @private
             */
            commonDesc__() {
                if (!this.backBtn) {
                    return ''
                }

                return this.$root.getCtrlDef(this.control+'.root').info.commonDesc || ''
            }
        },
        watch: {
            opened(newVal) {
                if (!newVal) {
                    this.controls = this.groupControls(this.$root.getCtrlDefs())
                    this.backBtn = false
                    this.control = null
                }
            }
        },
        mounted() {
            // Why this is here and not in data(){} ? Because when it is in
            // "data" it return empty object (I assume because apps data is
            // not initialized yet?).
            this.controls = this.groupControls(this.$root.getCtrlDefs())
        }
    })
</script>

<style scoped>
    .ncfb-ml-h div {
        margin: 10px 0 0 0;
        color: #adadad;
        font-weight: 100;
        font-size: .9rem;
    }
    .ncfb-body {
        background: #FAFBFC;
        padding: 2vh 2vw 4vh 2vw;
        border-top: 1px solid #fff;
        font-size: 0;
    }
    .ncfb-ctrl-group {
        font-size: 12px;
        text-transform: uppercase;
        font-weight: bold;
        color: #aeb5bb;
        /*border-bottom: 1px solid #ececec;*/
        margin-bottom: 10px;
    }
    .ncfb-controls {
        display: inline-block;
        width: 66%;
        font-size: 16px;
        vertical-align: top;
        padding-top: 20px;
        margin-right: 4%;
    }
    .ncfb-info {
        display: inline-block;
        width: 30%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-info h1 {
        font-size: 18px;
        font-weight: 100;
        margin-top: 26px;
        margin-bottom: 10px;
    }
    .ncfb-desc >>> * {
        line-height: 20px;
        color: #78828a;
    }
    .ncfb-desc >>> p {
        margin-bottom: 10px;
    }
    .ncfb-desc >>> ul {
        margin-left: 30px;
        margin-bottom: 10px;
    }
    .ncfb-desc >>> .ncfb-valid {
        display: block;
    }
    .ncfb-desc >>> .ncfb-valid * {
        color: #8bc34a;
    }
    .ncfb-desc >>> .ncfb-valid span, .ncfb-desc >>> .ncfb-not span {
        font-weight: bold;
    }
    .ncfb-desc >>> .ncfb-not {
        display: block;
    }
    .ncfb-desc >>> .ncfb-not * {
        color: #FF5722;
    }
    .ncfb-control {
        text-align: center;
        display: inline-block;
        padding: 10px;
        border-radius: 6px;
        vertical-align: middle;
        width: 110px;
        border: 1px solid transparent;
        margin-bottom: 10px;
    }
    .ncfb-control, .ncfb-control >>> * {
        cursor: pointer;
    }
    .ncfb-control:hover {
        background: rgba(255,255,255,.8);
        box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, .2);
        border: 1px solid #fff;
    }
    .ncfb-control > div {
        width: 100%;
        height: 32px;
        position: relative;
    }
    .ncfb-control > div >>> svg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    .ncfb-control > span {
        display: block;
        font-size: 12px;
        padding-top: 10px;
        color: gray;
        height: 38px;
        overflow: hidden;
        width: 100%;
    }
</style>



