<template>
    <div class="ncfb-of-cnt">
        <div class="ncfb-of-n">
            <a :class="{ 'ncfb-of-n-a': tab == 'basic' }" @click="tab = 'basic'">Basic data</a>
            <a :class="{ 'ncfb-of-n-a': tab == 'validation' }" @click="tab = 'validation'">Validation</a>
            <a :class="{ 'ncfb-of-n-a': tab == 'values' }" @click="tab = 'values'">Values</a>
        </div>
        <div class="ncfb-of-f">
            <div v-show="tab == 'basic'">
                <h2 class="ncfb-of-h">Basic</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Label</label>
                    <input type="text" class="ncfb-of-i" :value="data.label" prop="label" @input="vchange($event)" @blur="setDefault($event)" ref="label">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Help text</label>
                    <input type="text" class="ncfb-of-i" :value="data.help" prop="help" @input="vchange($event)" @blur="setDefault($event)">
                </div>
                <div class="ncfb-of-g" v-if="$root.listSetting">
                    <label class="ncfb-of-l">Display on list</label>
                    <ui-checkbox :checked="data.onList" @change="vchange($event)" prop="onList"></ui-checkbox>
                </div>
            </div>
            <div v-show="tab == 'validation'">
                <h2 class="ncfb-of-h">Validation</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Required</label>
                    <ui-checkbox :checked="data.required" @change="vchange($event)" prop="required"></ui-checkbox>
                </div>
            </div>
            <div v-show="tab == 'values'">
                <h2 class="ncfb-of-h">Values</h2>
                <div class="ncfb-of-g ncfb-list">
                    <transition-group name="animated-list" tag="div">
                        <div class="ncfb-list-item" v-for="value in data.values" :key="value.uid">
                            <ui-radio :checked="data.selected" @change="setSelected($event.value)" :value="value.uid" :ncfbhint="radioHint(value.uid == data.selected)"></ui-radio>
                            <input type="text" :value="value.value" @input="updateValue(value.uid, $event.target.value)">
                            <ui-btn type="list-up" @click="moveValue('up', value.uid)"></ui-btn>
                            <ui-btn type="list-down" @click="moveValue('down', value.uid)"></ui-btn>
                            <ui-btn type="list-rm" @click="removeValue(value.uid)"></ui-btn>
                        </div>
                        <div class="actions" :key="'a'">
                            <ui-btn type="add" text="Add value" @click.native="addValue()"></ui-btn>
                            <ui-btn type="reset" text="Reset default" @click.native="resetDefault()"></ui-btn>
                        </div>
                    </transition-group>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * Properties editor for controls type of "select".
     */
    import AbstractProps from 'evforms/components/controls/AbstractProps'
    import UiIcon from './../../ui/Icon'
    import UiRadio from './../../ui/Radio'
    import UiSelect from './../../ui/Select'
    import UiBtn from './../../ui/Btn'
    import UiCheckbox from './../../ui/Checkbox'
    import { get, each } from './../../../util/util'

    export default {
        extends: AbstractProps,
        name: 'fb-select-props',
        components: { UiIcon, UiBtn, UiSelect, UiRadio, UiCheckbox },
        methods: {
            getValues() {
                return this.$root.findItem(this.data.uid).item.values
            },
            addValue() {
                this.getValues().push({
                    uid: this.$root.findItem(this.data.uid).item.itemsUidIndex++,
                    value: ''
                })
                this.$root.$emit('change', { type: 'control-update' })
            },
            removeValue(uid) {
                const { index } = this.findItem(uid);
                this.getValues().splice(index, 1)
                this.$root.$emit('change', { type: 'control-update' })
            },
            updateValue(uid, value) {
                const { item } = this.findItem(uid);
                this.$set(item, 'value', value)
                this.$root.$emit('change', { type: 'control-update' })
            },
            moveValue(mode, uid) {
                const { index } = this.findItem(uid);
                const values = this.getValues();

                let newIndex = index;

                // Do nothing if it's first value and mode is up
                // or it's last value and mode is down.
                if (index === 0 && mode == 'up' || index === (values.length - 1) && mode == 'down' ) {
                    return
                }

                mode == 'down' ? newIndex++ : newIndex--

                values.splice(newIndex, 0, values.splice(index, 1)[0])
                this.$root.$emit('change', { type: 'control-update' })
            },
            /**
             * Find item by uid in values.
             * @param uid
             * @returns {*}
             */
            findItem(uid) {
                const result = each(this.getValues(), (item, idx) => {
                    if (item.uid == uid) {
                        return [idx, item]
                    }
                })

                if (result.length) {
                    return { index: result[0][0], item: result[0][1] }
                }

                return {}
            },
            setSelected(value) {
                this.$root.findItem(this.data.uid).item.selected = value
            },
            radioHint(selected) {
                return selected ? 'Selected by default' : 'Not selected'
            },
            resetDefault() {
                this.$root.findItem(this.data.uid).item.selected = null
            }
        }
    }
</script>

<style scoped>
    @import './../../../css/options-form.css';

    .ncfb-list {
        margin-left: 20%;
    }

    .ncfb-list-item {
        margin-bottom: 10px;
        transition: all 400ms;
    }

    .ncfb-list input {
        display: inline-block;
        vertical-align: middle;
        border: 1px solid #e6e6e6;
        width: 300px;
        height: 35px;
        padding: 0 10px;
        margin-right: 10px;
    }

    .ncfb-list-item .ncfb-radio {
        margin-right: 5px;
    }

    .ncfb-list .btn.is-up .ncfb-icon {
        transform: rotate(180deg);
    }

    .actions {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: 30px 0 0 0;
    }

    .actions .ncfb-add {
        margin-right: 10px;
    }

    .animated-list-enter {
        opacity: 0;
        transform: translateX(30px);
    }

    .animated-list-leave-active {
        position: absolute;
    }
</style>



