<template>
    <div class="ncfb-cols" :class="[colsNumCss__]">
        <template v-for="(nothing, idx) in colsNum__">
            <div class="ncfb-col" :class="['ncfb-col'+(idx+1)]">
                <a
                    class="ncfb-add"
                    v-if="data.items[idx].length == 0"
                    @mouseover.stop="$root.addTip('section.add', 'Click to add field to section')"
                    @mouseout.stop="$root.removeTip('section.add')"
                    @click="controlSelect(idx)">Add field</a>
                <CellTransition>
                    <fb-cell
                            v-for="(item, index) in data.items[idx]"
                            :key="item.uid"
                            :index="index"
                            :control="item"
                            :dropzoneRenderTop="index == 0"
                    ></fb-cell>
                </CellTransition>
            </div>
        </template>
    </div>
</template>
<script>
    import Vue from 'vue'
    import Props from './Props'
    import FbCell from '../../cell/BuilderCtrl'
    import CellTransition from '../../CellTransition'
    import { clone, extend } from '../../../util/util'

    const Ctrl = Vue.extend({
        name: 'fb-cols',
        components: { FbCell, CellTransition },
        props: {
            data: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                cell: {
                    //vapid: true,
                    headerStyle: {
                        borderLeft: '1px dashed #e6e6e6',
                        borderRight: '1px dashed #e6e6e6'
                    },
                    rootStyle: {
                        background: 'transparent'
                    },
                    cntStyle: {
                        padding: '2vh',
                        // borderLeft: '1px dashed #e6e6e6',
                        // borderRight: '1px dashed #e6e6e6',
                        // borderBottom: '1px dashed #e6e6e6',
                        // borderRadius: '6px'
                    }
                }
            }
        },
        methods: {
            controlSelect(colIdx) {
                this.$root.ctrlSelector.open({
                    target: this.data.uid,
                    insertMode: 'into',
                    options: {
                        col: colIdx
                    }
                })
            },
            createGhosts() {
                const data = this.data
                // Create ghost controls (not visible controls for dragging and dropping
                // controls into empty columns container).
                for (let i = 0; i < data.cols; i++) {
                    // Add ghost only for empty columns.
                    if (data.items[i].length === 0) {
                        this.$root.addCtrl(data.uid, 'ghost.ghost', 'into', {col: i})
                    }
                }
            }
        },
        computed: {
            /**
             * Return css class name based on number of items.
             *
             * @computed colsNumCss__
             */
            colsNumCss__() {
                return 'ncfb-cols-' + this.data.cols
            },
            colsNum__() {
                return new Array(parseInt(this.data.cols))
            }
        },
        mounted() {
            const me = this

            me.$emit('configure', me)
            me.createGhosts()
        },
        watch: {
            // When number of columns changes create ghosts if needed.
            'data.cols'() {
                this.createGhosts()
            },
            // When the only existing item is removed from items recreate ghosts.
            'data.items'() {
                this.createGhosts()
            }
        }
    })

    Ctrl.ALIAS = 'cnt'
    const data = {
        border: false,
        separation: false,
        spaceless: false,
        // Number of columns.
        cols: 1,
        items: [
            // Max 4 columns.
            [], [], [], []
        ]
    }
    // Common validators rules.
    const validators = {
        border: ['bool'],
        separation: ['bool'],
        cols: ['int', ['min', 1], ['max', 4]]
    }
    const info = {
        internal: false,
        desc: '',
        icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 15"><path fill="#fff" stroke="#6e6e6e" stroke-dasharray="2 2 0 0" d="M.5.5h24.8v14H.5z"/></svg>'
    }
    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Container',
            commonDesc: `
                Group your fields.
            `,
            desc: `
                <p>Container can be used in two main ways: </p>
                <ul>
                    <li>to visually group fields - eg. into bordered frame or display fields in two, three or four columns,</li>
                    <li>to logically group fields - container will be invisible in the form but in builder will allow you to group multiple fields and allow dragging them easier.</li>
                </ul>
                <p>Container can be used to:</p>
                <ul>
                    <li>organize fields into groups</li>
                    <li>organize fields into visually separate sections</li>
                    <li>organize fields into two, three or four columns</li>
                </ul>
            `
        }),
        // Plane container without styles; just structure container.
        cnt: extend(clone(info), {
            name: 'Container',
            data: extend(clone(data), {
                cols: 1
            }),
            validators: validators,
            desc: `
                <p>Container field can be used to:</p>
                <ul>
                    <li>organize fields into groups</li>
                    <li>organize fields into visually separate sections</li>
                    <li>organize fields into two, three or four columns</li>
                </ul>
            `
        }),
        two: extend(clone(info), {
            name: '2 columns',
            data: extend(clone(data), {
                cols: 2
            }),
            icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 15"><path fill="#fff" stroke="#6e6e6e" stroke-dasharray="2 2 0 0" d="M.5.5h24.8v14H.5z"/><path fill="none" stroke="#6e6e6e" stroke-dasharray="2 2 0 0" d="M12.9.5v14"/></svg>',
            validators: validators,
            desc: `
                <p>
                    This is preconfigured container to group fields into two columns.
                </p>
            `
        }),
        three: extend(clone(info), {
            name: '3 columns',
            data: extend(clone(data), {
                cols: 3
            }),
            icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 15"><path fill="#fff" stroke="#6e6e6e" stroke-dasharray="2 2 0 0" d="M.5.5h24.8v14H.5z"/><path fill="none" stroke="#6e6e6e" stroke-dasharray="2 2 0 0" d="M7.7 14.5V.5M17.3 14.5V.5"/></svg>',
            validators: validators,
            desc: `
                <p>
                    This is preconfigured container to group fields into three columns.
                </p>
            `
        }),
        four: extend(clone(info), {
            name: '4 columns',
            data: extend(clone(data), {
                cols: 4
            }),
            icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 15"><path fill="#fff" stroke="#6e6e6e" stroke-dasharray="2 2 0 0" d="M.5.5h24.8v14H.5z"/><path fill="none" stroke="#6e6e6e" stroke-dasharray="2 2 0 0" d="M19.1 14.5V.5M12.9 14.5V.5M6.7 14.5V.5"/></svg>',
            validators: validators,
            desc: `
                <p>
                    This is preconfigured container to group fields into four columns.
                </p>
            `
        }),
    }

    Ctrl.onCtrlAdd = function(target, control, insertMode, options) {
        if (target && target.item.type.indexOf('cnt.') === 0 && insertMode === 'into') {
            target.item.items[options.col].push(control)
            return true
        }
    }
    // Ctrl.onCtrlUpdate = function(item, key, value) {
    //     if (item.type.indexOf('section.') === 0) {
    //         if (key === 'items') {
    //             item.items.
    //
    //             return true
    //         }
    //     }
    // }
    export default Ctrl
</script>
<style scoped>
    .ncfb-cols {
        font-size: 0;
        text-align: center;
    }
    .ncfb-cols-1 > .ncfb-col {
        width: 100%;
    }
    .ncfb-cols-2 > .ncfb-col {
        width: 50%;
    }
    .ncfb-cols-3 > .ncfb-col {
        width: 33%;
    }
    .ncfb-cols-4 > .ncfb-col {
        width: 25%;
    }
    .ncfb-col {
        font-size: 16px;
        display: inline-block;
        vertical-align: top;
        padding-left: 1%;
    }
    .ncfb-col1 {
        margin-left: -1%;
    }
    .ncfb-add {
        font-size: 14px;
        background: #8bc34a;
        color: #fff;
        padding: 1.6vh 1vw;
        border-radius: 24px;
        display: inline-block;
        margin: 8vh 0 2vh 0;
        cursor: pointer;
    }
    .ncfb-add:hover {
        background: #9edb4f;
    }
    .cells-move {
        transition: all 1s;
    }
</style>



