<template>
    <transition name="bounce">
        <div class="ncfb-ml" v-show="opened">
            <NcfIcon name="close" color="#e6e6e6" class="ncfb-ml-ci" @click.native="close()"></NcfIcon>
            <div class="ncfb-ml-h">
                <h1>{{ title }}</h1>
                <div v-if="$slots.description">
                    <slot name="description"></slot>
                </div>
            </div>
            <div class="ncfb-body">
                <slot name="body"></slot>
            </div>
            <div class="ncfb-ml-bs">
                <slot name="footer"></slot>
                <button @click="close()" class="ncfb-ml-c">Close</button>
            </div>
        </div>
    </transition>
</template>

<script>
    import Vue from 'vue'
    import NcfIcon from '../ui/Icon'
    import { each } from './../../util/util'

    let modals = []

    export default Vue.component('ncfs-modal', {
        components: { NcfIcon },
        props: {
            // Modal title.
            title: String,
            // Resolve if modal is visible or not.
            opened: {
                type: Boolean,
                default: false
            }
        },
        methods: {
            updatePosition() {
                const
                    me = this,
                    $el = me.$el,
                    appWidth = me.$root.$el.offsetWidth,
                    margin = appWidth * .1,
                    scrollTop = window.pageYOffset || document.documentElement.scrollTop,
                    rootPos = me.$root.$el.getBoundingClientRect();

                $el.style.width = appWidth - margin + 'px'
                $el.style.left = margin / 2 + 'px'
                $el.style.top = scrollTop - (scrollTop + rootPos.y) + 40 + 'px'
            },
            close() {
                this.$emit('update:opened', false)
            }
        },
        mounted() {
            this.updatePosition()

            // Add modal instance to array of opened modals.
            if (this.opened) {
                modals.push(this)
            }
        },
        watch: {
            opened(newVal) {
                if (newVal) {
                    this.updatePosition()
                    // Close any other opened modal.
                    each(modals, (modal) => {
                        if (modal._uid !== this._uid) {
                            modal.close()
                        }
                    })

                    modals = []
                    // Add modal instance to array of opened modals.
                    modals.push(this)
                }
            }
        }
    })
</script>




