<template>
    <transition name="bounce">
        <div class="ncfb" v-show="opened" :style="{ 'z-index': builder.previewZIndex }">
            <div class="ncfb-preview">
                <FbIcon name="close" color="#e6e6e6" class="ncfb-ml-ci" @click.native="close"></FbIcon>
                <div class="ncfb-heading">
                    <div class="ncfb-left">
                        <h1>Form preview</h1>
                        <p>This is how your form will look like. <span v-if="Object.keys(builder.themes).length > 1">On the right side you can select theme you like the most.</span></p>
                    </div>
                    <div class="ncfb-right" v-if="Object.keys(builder.themes).length > 1">
                        <label class="ncfb-select-lbl">Theme</label>
                        <select class="ncfb-select" @change="themeChange($event.target.value)">
                            <option v-for="(theme, uid) in builder.themes" :value="uid" :selected="uid == builder.form.theme">{{ theme.name }}</option>
                        </select>
                        <br>
                        <a class="ncfb-use" v-show="showUseThemeBtn" @click="useTheme">use this theme</a>
                    </div>
                </div>
                <div class="ncfb-form" id="form-body" ref="form"><div></div></div>
            </div>
        </div>
    </transition>
</template>

<script>
    import Vue from 'vue'
    import $ from 'cash-dom'
    import FbForm from './Form'
    import FbIcon from './ui/Icon'
    import { clone, isFn } from '../util/util'

    export default Vue.extend({
        name: 'FbPreviewModal',
        components: { FbForm, FbIcon },
        props: {
            builder: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                opened: false,
                showUseThemeBtn: false
            }
        },
        methods: {
            open(o) {
                const
                    me = this,
                    $el = me.$el,
                    builder = me.builder,
                    scrollTop = window.pageYOffset || document.documentElement.scrollTop;

                if (me.opened) {
                    return
                }

                if (!me.formInstance) {
                    const reqData = {}
                    reqData[builder.formHandlerPreviewParam] = 1
                    me.formInstance = new FbForm({
                        el: me.$refs.form.children[0],
                        propsData: {
                            form: clone(builder.form),
                            url: builder.formHandlerUrl,
                            themes: builder.themes,
                            requestData: reqData,
                            onSubmit() {
                                if (isFn(builder.onPreviewSubmit)) {
                                    builder.onPreviewSubmit.call(builder, this)
                                }
                            }
                        }
                    })
                }

                //$el.style.width = appWidth - margin + 'px'
                //$el.style.left = margin / 2 + 'px'
                $el.style.top = (scrollTop + 40) + 'px'
                me.mask = $('<div class="ncfb-preview-mask"></div>').appendTo('body')

                me.$nextTick(() => {
                    me.mask.css('opacity', 1)
                })

                setTimeout(() => {
                    $('html').on('click.previewmodal', function(e) {
                        // Click wasn't made inside modal.
                        if (me.opened && !$(e.target).closest(me.$el).length) {
                            me.close()
                        }
                    })
                }, 500)

                me.opened = true
            },
            close() {
                const me = this
                if (!me.opened) {
                    return
                }
                me.formInstance.$destroy()
                me.formInstance = null
                me.$refs.form.innerHTML = '<div></div>'

                me.removeCloseListener()
                me.opened = false
                me.mask.remove()
            },
            removeCloseListener() {
                $('html').off('click.previewmodal')
            },
            themeChange(theme) {
                const me = this

                me.formInstance.useTheme(theme)
                me.updateUseThemeBtn()
            },
            updateUseThemeBtn() {
                this.showUseThemeBtn = this.builder.form.theme != this.formInstance.theme_
            },
            useTheme() {
                this.builder.updateForm('theme', this.formInstance.theme_)
                this.updateUseThemeBtn()
            }
        },
        destroyed() {
            this.removeCloseListener()
        }
    })
</script>

<style>
    .ncfb-preview-mask {
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: 999;
        background: rgba(0, 0, 0, .4);
        top: 0;
        left: 0;
        transition: all 800ms ease-out;
        opacity: 0;
    }
</style>
<style scoped>
    .ncfb {
        font-family: Arial, sans-serif;
        position: absolute;
        z-index: 1000;
        top: 1%;
        width: 60%;
        left: 50%;
        margin-left: -30%;
        background: #fff;
        box-shadow: 0 4px 9px -1px rgba(0, 0, 0, .1);
    }
    .ncfb-preview {
        /*position: absolute;
        z-index: 1000;
        top: 1%;
        left: 50%;
        transform: translateX(-50%);*/
        /*background: #fff;
        box-shadow: 0 4px 9px -1px rgba(0, 0, 0, .1);
        width: 60%;*/
    }
    @media (max-width: 1500px) {
        .ncfb-app {
            width: 98%;
            margin-left: -49%;
        }
    }
    .ncfb-heading {
        background: #8BC34A;
        display: inline-block;
        width: 100%;
        font-size: 0;
        padding: 1vh 2vw;
        position: relative;
    }
    .ncfb-left {
        display: inline-block;
        width: 70%;
        font-size: 16px;
        vertical-align: middle;
    }
    .ncfb-right {
        text-align: right;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 46px;
    }
    .ncfb-heading h1 {
        color: #fff;
        font-size: 20px;
        margin: 20px 0 6px 0;
    }
    .ncfb-heading p {
        color: rgba(255, 255, 255, .7);
        margin: 0 0 20px 0;
    }
    .ncfb-select {
        padding: 1.4vh 2vh;
        border: 0;
        background: #fff;
        margin-right: 20px;
        border-radius: 10px;
        font-size: 18px;
    }
    .ncfb-select-lbl {
        color: #fff;
        margin-right: 10px;
        font-size: 20px;
    }
    .ncfb-use {
        color: yellow;
        text-decoration: underline;
        font-size: .8em;
        display: inline-block;
        margin: 10px 20px 0 0;
        cursor: pointer;
    }
    .ncfb-form {
        padding: 5vh 4vw 7vh 4vw;
    }
</style>



