<template>
    <div class="ncfb-of-cnt">
        <div class="ncfb-of-n">
            <a :class="{ 'ncfb-of-n-a': tab == 'basic' }" @click="tab = 'basic'">Basic data</a>
            <a :class="{ 'ncfb-of-n-a': tab == 'validation' }" @click="tab = 'validation'">Validation</a>
        </div>
        <div class="ncfb-of-f">
            <div v-show="tab == 'basic'">
                <h2 class="ncfb-of-h">Basic data</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Label</label>
                    <input type="text" class="ncfb-of-i" :value="data.label" prop="label" @input="vchange($event)" @blur="setDefault($event)" ref="label">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Help text</label>
                    <input type="text" class="ncfb-of-i" :value="data.help" prop="help" @input="vchange($event)" @blur="setDefault($event)">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Placeholder</label>
                    <input type="text" class="ncfb-of-i" :value="data.placeholder" prop="placeholder" @input="vchange($event)" @blur="setDefault($event)">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Size</label>
                    <ui-select
                            class="ncfb-of-i"
                            items-width="400px"
                            :items="$options.sizes"
                            id-key="id"
                            name-key="name"
                            :value="data.size"
                            @change="$root.updateCtrl(data.uid, 'size', $event.value.id)">
                        <template slot="items" slot-scope="{ item }">
                            <div class="ncfb-selectsmall-item">
                                <span class="ncfb-selectsmall-item-name">{{ item.name }}</span>
                            </div>
                        </template>
                    </ui-select>
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Default value</label>
                    <textarea class="ncfb-of-i" :value="data.default" prop="default" @input="vchange($event)" @blur="setDefault($event)"></textarea>
                </div>
                <div class="ncfb-of-g" v-if="$root.listSetting">
                    <label class="ncfb-of-l">Display on list</label>
                    <ui-checkbox :checked="data.onList" @change="vchange($event)" prop="onList"></ui-checkbox>
                </div>
            </div>
            <div v-show="tab == 'validation'">
                <h2 class="ncfb-of-h">Validation</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Required</label>
                    <ui-checkbox :checked="data.required" @change="vchange($event)" prop="required"></ui-checkbox>
                </div>
                <div v-if="data.valueType == 'any'">
                    <div class="ncfb-of-g2">
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="Minimum number of characters that must be entered in this field"></fb-icon>
                            <label class="ncfb-of-l">Min</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.any.min" prop="valueTypeCfg.any.min" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="Maximum number of characters that can be entered in this field"></fb-icon>
                            <label class="ncfb-of-l">Max</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.any.max" prop="valueTypeCfg.any.max" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * Properties editor for controls type of "textArea".
     */
    import AbstractProps from 'evforms/components/controls/AbstractProps'
    import FbIcon from './../../ui/Icon'
    import UiCheckbox from './../../ui/Checkbox'
    import UiSelect from './../../ui/Select'
    import $ from 'cash-dom'
    import { get, each } from './../../../util/util'

    export default {
        extends: AbstractProps,
        name: 'fb-text-area-props',
        components: { FbIcon, UiCheckbox, UiSelect },
        valueTypes: [
            {
                id: 'any',
                name: 'Any value',
                desc: 'Any provided value is accepted.'
            }
        ],
        sizes: [
            {
                id: 'sm',
                name: 'Small',
            },
            {
                id: 'md',
                name: 'Medium',
            },
            {
                id: 'lg',
                name: 'Large',
            }
        ],
        methods: {
            valueType(id) {
                return this.valueTypesMap[id]
            }
        },
        created() {
            this.valueTypesMap = {}
            each(this.$options.valueTypes, (type) => {
                this.valueTypesMap[type.id] = type
            })
        }
    }
</script>

<style scoped>
    @import './../../../css/options-form.css';

    .ncfb-of-desc span {
        padding-left: 0;
        width: auto;
        display: inline-block;
    }


    .ncfb-selectsmall-item {
        display: block;
        border-bottom: 1px solid #ececec;
        transition: background-color 100ms ease-out;
        cursor: pointer;
        background: #fafbfc;
    }
    .ncfb-selectsmall-item:hover {
        background-color: #e7f2f5;
    }
    .ncfb-select-item:hover em {
    }
    .ncfb-selectsmall-item-name {
        padding: 10px 20px;
        font-weight: bold;
        display: block;
        background: #fff;
    }
    .ncfb-ok {
        display: block;
        color: gray;
        padding: 6px 20px 4px 20px;
    }
    .ncfb-ok strong {
        font-weight: normal;
        color: #10c506;
    }
    .ncfb-not {
        display: block;
        color: gray;
        padding: 4px 20px 6px 20px;
    }
    .ncfb-not strong {
        font-weight: normal;
        color: #da2764;
    }
</style>



