<template>
    <div class="ncfb-cols" :class="[colsNumCss__]">
        <template v-for="(nothing, idx) in colsNum__">
            <div class="ncfb-col" :class="['ncfb-col'+(idx+1)]" ref="cols">
                <div
                    v-for="(item, index) in data.items[idx]"
                    :key="item.uid">
                    <fb-cell
                        v-if="$root.hasCtrlDef(item.type)"
                        :index="index"
                        :control="item"
                        :dropzoneRenderTop="index == 0"
                        ref="cells"
                    ></fb-cell>
                </div>
            </div>
        </template>
    </div>
</template>
<script>
    import Vue from 'vue'
    import FbCell from '../../cell/FormCtrl'
    import { each, extend } from '../../../util/util'

    const Ctrl = Vue.extend({
        name: 'fb-cols',
        components: { FbCell },
        props: {
            data: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                cell: {
                    //vapid: true,
                    headerStyle: {
                        borderLeft: '1px dashed #e6e6e6',
                        borderRight: '1px dashed #e6e6e6'
                    },
                    rootStyle: {
                        background: 'transparent'
                    },
                    cntStyle: {
                        padding: '2vh',
                        // borderLeft: '1px dashed #e6e6e6',
                        // borderRight: '1px dashed #e6e6e6',
                        // borderBottom: '1px dashed #e6e6e6',
                        // borderRadius: '6px'
                    }
                }
            }
        },
        methods: {
            value() {
                const values = {}
                each(this.$refs.cells, (cell) => {
                    const value = cell.value()
                    if (value !== null) {
                        extend(values, cell.value())
                    }
                })
                return values
            },
            fullWidth(items, splitted = []) {
                const
                    width = this.$parent.$el.clientWidth,
                    cols = this.data.cols;

                for (let i = 0; i < items.length; i++) {
                    const item = items[i]

                    if (item.type.indexOf('cnt.') === 0) {
                        if (item.cols > 1) {
                            if (item.uid == this.data.uid) {
                                 if (!splitted.length) {
                                    if (cols == 3) {
                                        let add = width / 3 < 300
                                        each(this.$refs.cols, (col) => {
                                            col.classList[add ? 'add' : 'remove']('ncfb-col-full')
                                        })
                                    } else if (cols == 2) {
                                        let add = width / 2 < 300
                                        each(this.$refs.cols, (col) => {
                                            col.classList[add ? 'add' : 'remove']('ncfb-col-full')
                                        })
                                    }
                                }

                                return
                            } else {
                                splitted.push(item)
                            }
                        }

                        for (let j = 0; j < item.cols; j++) {
                            this.fullWidth(item.items[j], splitted.slice())
                        }
                    }
                }
            }
        },
        computed: {
            /**
             * Return css class name based on number of items.
             *
             * @computed colsNumCss__
             */
            colsNumCss__() {
                return 'ncfb-cols-' + this.data.cols
            },
            colsNum__() {
                return new Array(parseInt(this.data.cols))
            }
        },
        mounted() {
            const me = this

            me.$emit('configure', me)

            me.interval = setInterval(() => {
                me.fullWidth(me.$root.form.items)
            }, 1000)
        },
        beforeDestroy() {
            clearInterval(this.interval)
            this.interval = null
        }
    })

    Ctrl.ALIAS = 'section'
    export default Ctrl
</script>
<style scoped>
    .ncfb-cols {
        font-size: 0;
        /*text-align: center;*/
    }
    .ncfb-cols-1 > .ncfb-col {
        width: 100%;
    }
    .ncfb-cols-2 > .ncfb-col {
        width: 49%;
    }
    .ncfb-cols-2 > .ncfb-col1 {
        margin-right: 1%;
    }
    .ncfb-cols-2 > .ncfb-col2 {
        margin-left: 1%;
    }
    .ncfb-cols-3 > .ncfb-col {
        width: 33%;
    }
    .ncfb-cols-4 > .ncfb-col {
        width: 25%;
    }
    .ncfb-col {
        font-size: 16px;
        display: inline-block;
        vertical-align: top;
        padding-left: 1%;
    }
    .ncfb-col1 {
        margin-left: -1%;
    }
    .ncfb-col-full {
        width: 100% !important;
        margin-left: 0 !important;
        padding-left: 0 !important;
    }
    .ncfb-add {
        font-size: 14px;
        background: #8bc34a;
        color: #fff;
        padding: 1.6vh 1vw;
        border-radius: 24px;
        display: inline-block;
        margin: 8vh 0 2vh 0;
        cursor: pointer;
    }
    .ncfb-add:hover {
        background: #9edb4f;
    }
    .cells-move {
        transition: all 1s;
    }
</style>



