<template>
    <div class="ncfb-of-cnt">
        <div class="ncfb-of-n">
            <a :class="{ 'ncfb-of-n-a': tab == 'basic' }" @click="tab = 'basic'">Basic data</a>
        </div>
        <div class="ncfb-of-f">
            <div v-show="tab == 'basic'">
                <h2 class="ncfb-of-h">Basic data</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Number of columns</label>
                    <select class="ncfb-of-i" :value="data.cols" prop="cols" @input="vchange($event)">
                        <option value="1">None</option>
                        <option value="2">Two</option>
                        <option value="3">Three</option>
                        <option value="4">Four</option>
                    </select>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * Properties editor for controls type of "textArea".
     */
    import AbstractProps from 'evforms/components/controls/AbstractProps'
    import FbIcon from './../../ui/Icon'
    import FbCheckbox from './../../ui/Checkbox'
    import UiSelect from './../../ui/Select'
    import $ from 'cash-dom'
    import { get, each } from './../../../util/util'

    export default {
        extends: AbstractProps,
        name: 'fb-cnt-props',
        components: { FbIcon, FbCheckbox, UiSelect }
    }
</script>

<style scoped>
    @import './../../../css/options-form.css';

    .ncfb-of-desc span {
        padding-left: 0;
        width: auto;
        display: inline-block;
    }


    .ncfb-selectsmall-item {
        display: block;
        border-bottom: 1px solid #ececec;
        transition: background-color 100ms ease-out;
        cursor: pointer;
        background: #fafbfc;
    }
    .ncfb-selectsmall-item:hover {
        background-color: #e7f2f5;
    }
    .ncfb-select-item:hover em {
    }
    .ncfb-selectsmall-item-name {
        padding: 10px 20px;
        font-weight: bold;
        display: block;
        background: #fff;
    }
    .ncfb-ok {
        display: block;
        color: gray;
        padding: 6px 20px 4px 20px;
    }
    .ncfb-ok strong {
        font-weight: normal;
        color: #10c506;
    }
    .ncfb-not {
        display: block;
        color: gray;
        padding: 4px 20px 6px 20px;
    }
    .ncfb-not strong {
        font-weight: normal;
        color: #da2764;
    }
</style>



