<template>
    <h4 v-if="data.type === 'heading.heading4'" class="ncfb-heading-4" :style="{ textAlign: data.align }" v-text="data.value"></h4>
    <h3 v-else-if="data.type === 'heading.heading3'" class="ncfb-heading-3" :style="{ textAlign: data.align }" v-text="data.value"></h3>
    <h2 v-else-if="data.type === 'heading.heading2'" class="ncfb-heading-2" :style="{ textAlign: data.align }" v-text="data.value"></h2>
    <h1 v-else class="ncfb-heading-1" :style="{ textAlign: data.align }" v-text="data.value"></h1>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-heading',
        methods: {
            value() {
                return null
            }
        }
    })

    Ctrl.ALIAS = 'heading'
    export default Ctrl
</script>



