<template>
    <div class="ncfb-nav">
        <div class="ncfb-left">
            <h1 class="ncfb-heading" v-if="$root.nameOption" @click="$root.openSettings('basic', 'name')">{{ $root.form.name }} <ui-icon name="edit" color="#fff" width="20"></ui-icon></h1>
            <span class="ncfb-desc" v-if="$root.descriptionOption">{{ $root.form.description }}</span>
        </div>
        <div class="ncfb-right">
            <button type="button" @click="$root.openPreview()" class="ncfb-preview ncfb-btn">
                <ui-icon name="desktop" color="#fff" width="16"></ui-icon>
                <span>preview</span>
            </button>
            <button type="button" @click="$root.openSettings('basic', 'name')" class="ncfb-btn">
                <ui-icon name="settings" color="#fff" width="16"></ui-icon>
                <span>form settings</span>
            </button>
            <ui-icon @click.native="$root.exportModal = true" hint="Export form" name="download" class="ncfb-download" width="20"></ui-icon>
            <ui-icon @click.native="$root.importModal = true" hint="Import form" name="upload" class="ncfb-upload" width="20"></ui-icon>
            <ui-icon @click.native="reset()" class="ncfb-reset" name="reset" color="#fff" width="14" hint="Reset form"></ui-icon>
        </div>
    </div>
</template>

<script>
    import UiIcon from './ui/Icon'

    export default {
        name: 'fb-nav',
        components: { UiIcon },
        props: ['cols'],
        methods: {
            reset() {
                if (confirm('Are you sure you want to reset form? All fields will be removed.')) {
                    this.$root.form.items = []
                    this.$root.$emit('change', { type: 'reset' })
                }
            },
        }
    }
</script>

<style scoped>
    .ncfb-nav {
        width: 100%;
        padding: 10px 16px;
        background: #88a7f1;
        border-radius: 10px;
        color: #fff;
        margin-top: 17px;
    }

    .ncfb-nav:after {
        display: table;
        content: " ";
        clear: both;
    }
    .ncfb-nav:before {
        display: table;
        content: " ";
    }

    .ncfb-left {
        float: left;
        width: 50%;
    }

    .ncfb-heading {
        font-weight: 100;
        font-size: 20px;
        margin: 6px 0 0 6px;
        cursor: pointer;
    }

    .ncfb-desc {
        display: block;
        margin-left: 6px;
        opacity: .8;
    }

    .ncfb-right {
        float: right;
        width: 50%;
        text-align: right;
    }

    label {
        text-transform: uppercase;
        font-size: .8rem;
        margin-right: 10px;
    }
    .ncfb-btn {
        color: #fff;
        border-radius: 6px;
        background: transparent;
        font-family: Archivo, sans-serif;
        font-size: .8rem;
        border: 1px solid #fff;
        padding: 6px 12px;
        outline: none;
        cursor: pointer;
        line-height: 16px;
    }
    button:hover {
        background: #fff;
        color: #88A7F1;
    }
    button:hover >>> path {
        fill: #88A7F1;
    }
    button span {
        display: inline-block;
        vertical-align: middle;
    }
    button .ncfb-icon {
        margin-right: 2px;
    }
    button.is-one {
        margin-right: -6px;
        border-radius: 4px 0 0 4px;
        border-right: 0;
    }
    button.is-two {
        border-left: 0;
        border-radius: 0 4px 4px 0;
    }
    button.is-active {
        background: #fff;
        border-color: transparent;
        color: #88A7F1;
    }
    .ncfb-preview {
        background: #8bc34a;
        border-color: #fff;
        margin-right: 6px;
    }
    .ncfb-reset {
        background: #f5893c;
        width: 28px;
        height: 31px;
        text-align: center;
        padding: 0;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 6px;
        margin-left: 10px;
        cursor: pointer;
    }
    .ncfb-download, .ncfb-upload {
        background: rgba(255, 255, 255, .6);
        text-align: center;
        padding: 4px 6px;
        display: inline-block;
        vertical-align: middle;
        border-radius: 6px;
        margin-left: 10px;
        cursor: pointer;
        line-height: 16px;
    }
    .ncfb-download:hover, .ncfb-upload:hover {
        background: #fff;
    }
    .ncfb-download > svg, .ncfb-upload > svg {
        position: relative;
        top: 1px;
    }
</style>



