import { extend, uuidv4 } from "./util";

const formDefaults = {
  // Form Builder version.
  version: '1.1',
  // Universally unique identifier.
  uuid: uuidv4(),
  // This can not never start with 0. Min value is "1".
  uidIndex: 100,
  // Form name.
  name: 'Unnamed form',
  // Form description.
  description: '',
  // Form theme.
  theme: 'ncfbdef',
  // Label position:
  // - above = Above the input
  // - left = On the left side of the input
  lblPos: 'above', // above or left
  // Help text as icon.
  helpIcon: false,
  // Submit button.
  submitBtn: {
    text: 'Send'
  },
  confirmation: {
    type: 'msg', // msg or redirect
    // Used only if type is "msg".
    msg: 'Thanks for filling out my form!',
    // Used only if type is "redirect"
    url: ''
  },
  items: [],
  // Configuration related with submission delivery to specified emails.
  emailDelivery: {
    // Array of emails to which send submission.
    emails: [],
    // Email subject.
    subject: "New form submission",
    // Default message.
    message: `
                <div>
                    <p>New form submission:</p>
                    %submission%
                </div>
            `
  }
};

/**
 * Apply default values for form definition object.
 * @param obj
 * @return {Object}
 */
export default function normalizeFormDef(obj) {
  let formDef;
  if (obj) {
    formDef = extend(formDefaults, JSON.parse(JSON.stringify(obj)), extend.DEEP)
  } else {
    formDef = formDefaults
  }

  if (!formDef.uuid) {
    formDef.uuid = uuidv4()
  }

  return formDef
}



