<template>
    <div class="ncfb-radios">
        <label v-if="data.label != false" class="ncfb-opt-label" @click="$root.openProps(data.uid, { tab: 'basic', focus: 'label' })">{{ data.label }}</label>
        <span v-if="data.help" class="ncfb-help">{{ data.help }}</span>
        <div :class="[layout__]">
            <div v-for="(group, groupIndex) in items__" :class="'ncfb-col ncfb-col-'+(groupIndex+1)">
                <div class="ncfb-radio" v-for="item in group">
                    <ui-radio :name="'rc-'+data.uid" :checked="data.default" :value="item.uid" :label="item.value"></ui-radio>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import UiRadio from './../../ui/Radio'
    import { extend, clone } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fbctrl-radios',
        components: { UiRadio },
        computed: {
            items__() {
                const
                    me = this,
                    data = me.data,
                    layout = data.layout,
                    colNum = (layout === '1-column' || layout === 'side-by-side') ? 1
                        : (layout === '2-column' ? 2
                            : (layout === '3-column' ? 3 : 1)),
                    arr = [],
                    items = data.items,
                    size = Math.ceil(items.length / colNum)
                let i = 0
                for (; i < items.length; i += size) {
                    arr.push(items.slice(i, i+size))
                }
                return arr
            },
            layout__() {
                return 'ncfb-layout-' + this.data.layout
            }
        }
    })

    Ctrl.ALIAS = 'radio'
    const data = {
        help: '',
        required: false,
        label: 'Label',
        // Available values: 1-column, 2-column, 3-column, side-by-side
        layout: '1-column',
        default: null,
        items: [
            { uid: 1, value: 'Yes' },
            { uid: 2, value: 'No' }
        ],
        itemsUidIndex: 3,
        // Display this field on results list.
        onList: false
    }

    const validators = {}

    const info = {
        internal: false,
        desc: '',
        icon: '<svg style="width: 20px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 16 16"><circle cx="7.5" cy="7.5" r="7" fill="#fff" stroke="#6e6e6e"/><path fill="none" stroke="#6e6e6e" d="M10.9 4.2L4.2 11M4.2 4l6.7 6.8"/></svg>'
    }

    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Multiple choice',
            group: 'inputs',
            commonDesc: `
                <p>Radio are generally used in radio groups—collections of radio buttons describing a set of related options. Only one radio button in a given group can be selected at the same time. Radio buttons are typically rendered as small circles, which are filled or highlighted when selected.</p>
            `,
            desc: `<p>Radio are generally used in radio groups—collections of radio buttons describing a set of related options. Only one radio button in a given group can be selected at the same time. Radio buttons are typically rendered as small circles, which are filled or highlighted when selected.</p>`
        }),
        radio: extend(clone(info), {
            name: 'Radio (multiple choice)',
            data: clone(data),
            validators: validators,
            desc: `<p>.</p>`
        })
    }

    export default Ctrl
</script>

<style scoped>
    label {
        padding: 0;
        margin: 0;
        line-height: 16px;
    }
    .ncfb-radios {
        display: block;
        vertical-align: middle;
        margin-bottom: 16px;
    }
    .ncfb-opt-label {
        margin: 10px 0 20px 0;
        display: inline-block;
    }
    .ncfb-help {
        font-size: 12px;
        display: inline-block;
        font-style: italic;
        color: gray;
        margin-bottom: 8px;
    }
    .ncfb-radio {
        width: 100%;
    }

    .ncfb-layout-2-column {
        font-size: 0;
    }
    .ncfb-layout-2-column .ncfb-col {
        display: inline-block;
        width: 50%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-layout-3-column .ncfb-col {
        display: inline-block;
        width: 33%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-layout-side-by-side .ncfb-radio {
        width: auto;
        display: inline-block;
        margin-right: 20px;
    }
</style>
<style>
    .ncfb-layout-side-by-side .ncfb-radio .ncfb-label {
        width: auto;
    }
</style>



