<template>
    <div class="ncfb-radio">
        <input ref="input" :name="name" type="radio" :id="id" class="ncfb-radio-i" style="display: none;" :checked="checked == value" @change="change" :value="value">
        <label :for="id" class="ncfb-radio-l"></label>
        <label :for="id" v-if="label" class="ncfb-label">{{ label }}</label>
    </div>
</template>

<script>
    let ids = 0;

    export default {
        name: 'ui-radio',
        props: {
            // Value of this radio button.
            value: {
                type: [String, Number],
                required: true
            },
            // Currently selected value.
            checked: {
                type: [String, Number],
                required: false
            },
            label: {
                type: String
            },
            name: {
                type: String,
                required: false
            }
        },
        data() {
            return {
                id: 'ncfb-rbn-' + ids++,
            }
        },
        methods: {
          change() {
              this.$emit('change', { cmp: this, value: this.value, target: this.$el })
          }
        }
    }
</script>

<style scoped>
    label {
        padding: 0;
        margin: 0;
        line-height: 16px;
    }
    .ncfb-radio {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        margin-bottom: 10px;
        width: 100%;
    }

    .ncfb-radio-l {
        display: inline-block;
        cursor: pointer;
        position: relative;
        width: 20px;
        height: 20px;
        font-size: 1em;
        color: #000;
        vertical-align: top;
    }

    .ncfb-radio-l:before {
        content: "";
        display: inline-block;
        width: 18px;
        height: 18px;
        border-radius: 100px;
        position: absolute;
        left: 0px;
        background-color: #ffffff;
        border: 1px solid #e2e2e2;
     }

    .ncfb-radio-l:after {
        position: absolute;
        top: 6px;
        left: 6px;
        border-radius: 100px;
        background: #526aa4;
        width: 8px;
        height: 8px;
    }

    .ncfb-radio input[type=radio]:checked+label::after {
        content: '';
    }

    .ncfb-label {
        display: inline-block;
        vertical-align: top;
        font-size: 13px;
        width: calc(100% - 30px);
        padding: 3px 5px 0 7px;
    }
</style>



