<template>
    <div class="ncfb-body">
        <hr />
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import { extend, clone } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-separator'
    })

    Ctrl.ALIAS = 'separator'
    const data = {

    }

    const validators = {}

    const info = {
        internal: false,
        desc: `
            <p>Allow to visually separate fields with horizontal line.</p>
        `,
        icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 1"><path fill="none" stroke="#6e6e6e" stroke-dasharray="2 2 0 0" d="M.5.5h24.2"/></svg>'
    }

    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Separator',
            group: 'misc'
        }),
        separator: extend(clone(info), {
            name: 'Separator',
            data: clone(data),
            validators: validators
        })
    }

    export default Ctrl
</script>
<style scoped>
    hr {
        background: #a2a2a2;
        height: 1px;
        margin: 10px;
    }
</style>



