<template>
    <div class="ncfb-body">
        <label @click="$root.openProps(data.uid, { tab: 'basic', focus: 'label' })">{{ data.label }}</label>
        <span v-if="data.help" class="ncfb-help">{{ data.help }}</span>
        <textarea class="ncfb-textarea" :placeholder="data.placeholder" :value="data.default"></textarea>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import { extend, clone } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-text-area'
    })

    Ctrl.ALIAS = 'textArea'

    const data = {
        label: 'Label',
        help: '',
        placeholder: '',
        required: false,
        default: '',
        size: 'md',
        valueType: 'any',
        valueTypeCfg: {
            any: {
                min: 0,
                max: 999999999
            },
        },
        // Display this field on results list.
        onList: false
    }

    const validators = {
        valueTypeCfg: {
            any: {
                min: ['int', ['min', 0], ['max', 999999]],
                max: ['int', ['min', 0], ['max', 999999]]
            }
        }
    }

    const info = {
        internal: false,
        desc: '',
        icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><g fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="6"><text x="150.1" y="37.1" transform="translate(-146.2 -30)" font-size="6">hello</text><text x="150.1" y="42.1" transform="translate(-146.2 -30)" font-size="6">world</text></g></svg>'
    }

    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Text area',
            group: 'inputs'
        }),
        textArea: extend(clone(info), {
            name: 'Text area',
            data: clone(data),
            validators: validators
        })
    }

    export default Ctrl
</script>
<style scoped>
    .ncfb-label {
        width: 80%;
    }
    .ncfb-body label {
        display: inline-block;
        margin: 10px 0;
    }
    .ncfb-help {
        font-size: 12px;
        display: inline-block;
        font-style: italic;
        color: gray;
        margin-bottom: 8px;
    }
    .ncfb-textarea {
        width: 100%;
        height: 80px;
        resize: vertical;
        padding: .5vw;
        border: 1px solid #c3c3c3;
        transition: all 500ms ease-out;
        outline: none;
        margin-bottom: 10px;
    }
    .ncfb-textarea:focus {
        border-color: #03A9F4;
    }
</style>



