<template>
    <div class="ncfb-textarea ncfb-ctrl" :class="{ 'ncfb-ctrl-error': error }">
        <component :is="cmpLbl" :label="data.label" :help="data.help"></component>
        <textarea
            class="ncfb-input"
            :class="textAreaCss__"
            :placeholder="data.placeholder"
            @input="error = false"
            ref="input"></textarea>
        <span v-if="data.help && $root.form.lblPos === 'left' && !$root.form.helpIcon" class="ncfb-help">{{ data.help }}</span>
        <span class="ncfb-error" v-show="error">{{ error }}</span>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../../renderer/AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-text-area',
        methods: {
            getValue() {
                return this.$refs.input.value
            }
        },
        computed: {
            textAreaSize__() {
                return 'ncfb-textarea-'+this.data.size
            },
            textAreaCss__() {
                const o = {
                    'ncfb-input-error': !!this.error
                }
                o['ncfb-textarea-'+this.data.size] = true
                return o
            }
        },
        mounted() {
            this.$refs.input.value = this.data.default
        }
    })

    Ctrl.ALIAS = 'textArea'
    export default Ctrl
</script>



