<template>
    <label class="ncfb-lbl" v-if="label">
        {{ label }}
        <span v-if="help && $root.form.lblPos !== 'left' && !$root.form.helpIcon" class="ncfb-help">{{ help }}</span>
        <ui-hint class="hint" :hint="help" v-if="help && $root.form.helpIcon">
            <ui-icon name="help" color="#9e9e9e" width="15"></ui-icon>
        </ui-hint>
    </label>
</template>
<script>
    import UiIcon from './../ui/Icon'
    import UiHint from './../ui/Hint'

    export default {
        components: { UiIcon, UiHint },
        props: {
            label: String,
            help: String
        }
    }
</script>

<style scoped>
    .ncfb-lbl {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: flex-start;
    }
    .ncfb-help {
        margin-left: 5px;
    }
    .hint {
        margin-left: 5px;
    }

    label {
        cursor: default;
    }
</style>



