import FormComposer from './FormComposer.vue'
import { isStr, isObj } from './util/util'

const ctx = window

// Create namespace.
const ns = ['nocake', 'form', 'builder']
let current = ctx

for (let i = 0; i < ns.length; i++) {
    const key = ns[i]
    if (!current[key]) current[key] = {}
    current = current[key]
}

const factory = {
    create(o) {
        let el = o.el
        if (!el) {
            throw new TypeError('[FormComposer] Form Composer requires "el" config defined.')
        }

        if (isStr(el)) {
            el = document.querySelector(el)
            if (!el) {
                throw new TypeError('[FormComposer] DOM Element provided in "el" config can not be found.')
            }
        }

        // Don't use "instanceof Element".
        if (!isObj(el) || !('nodeType' in el)) {
            throw new TypeError('[FormComposer] "el" config must be type of string or HTMLElement.')
        }

        if (!isObj(o.themes)) {
            throw new TypeError('[FormComposer] "themes" options missing.');
        }

        const div = document.createElement('div')
        el.appendChild(div)

        o.el = div

        const fc = new FormComposer({
            el: div,
            propsData: o
        })

        fc.destroy = function() {
            fc.$destroy()
            el.innerHTML = ''
        }

        fc.on = function(event, fn) {
            fc.$on(event, fn)
        }

        return fc
    }
}

ctx.nocake.form.builder = factory



