function int(value) {
    return (!isNaN(value) && value % 1 === 0)
}
int.msg = 'This is not integer.'

function min(value, min) {
    return value >= min
}
min.msg = 'Value can not be lower then {0}'

function max(value, max) {
    return value <= max
}
max.msg = 'Value can not be higher then {0}'

function bool(value) {
    return typeof value !== 'boolean'
}
bool.msg = 'Value should be type of boolean'

function oneof(value, options) {
    return options.indexOf(value) !== -1
}
oneof.msg = 'Value should be one of {0}'

export { int, min, max, bool, oneof }


