<template>
  <ncfs-modal :opened="opened" @update:opened="$emit('update:opened', $event)" title="Form settings">
    <template #body>
      <div class="ncfb-of-cnt">
        <div class="ncfb-of-n">
          <a :class="{ 'ncfb-of-n-a': tab == 'basic' }" @click="tab = 'basic'">Basic</a>
          <a :class="{ 'ncfb-of-n-a': tab == 'confirmation-options' }" @click="tab = 'confirmation-options'">Confirmation
            Options</a>
          <!-- Custom settings. -->
          <a v-for="(a, aKey) in settings__" :class="{ 'ncfb-of-n-a': tab == aKey }" @click="tab = aKey">{{
              a.name
            }}</a>
          <!-- EO custom settings. -->
        </div>
        <div class="ncfb-of-f">
          <div v-show="tab == 'basic'">
            <h2 class="ncfb-of-h">Basic</h2>
            <div v-if="$root.nameOption" class="ncfb-of-g">
              <label class="ncfb-of-l">Form name</label>
              <input type="text" class="ncfb-of-i" :value="form.name" @input="vchange($event)" prop="name"
                     ref="formName">
            </div>
            <div v-if="$root.descriptionOption" class="ncfb-of-g">
              <label class="ncfb-of-l">Form description</label>
              <textarea class="ncfb-of-i" :value="form.description" @input="vchange($event)"
                        prop="description"></textarea>
            </div>
            <div class="ncfb-of-g" v-if="Object.keys($root.themes).length > 1">
              <label class="ncfb-of-l">Theme</label>
              <select class="ncfb-of-i" @change="vchange($event)" prop="theme">
                <option v-for="(theme, uid) in $root.themes" :value="uid" :selected="uid == form.theme">{{ theme.name }}</option>
              </select>
            </div>
            <div class="ncfb-of-g">
              <label class="ncfb-of-l">Label placement</label>
              <ui-select
                  class="ncfb-of-i"
                  items-width="400px"
                  :items="$options.labelPlacementValues"
                  id-key="id"
                  name-key="name"
                  :value="form.lblPos"
                  prop="lblPos"
                  @change="vchange($event)">
                <template slot="items" slot-scope="{ item }">
                  <div class="ncfb-select-item">
                    <span class="ncfb-select-item-name">{{ item.name }}</span>
                    <em v-if="item.desc" class="ncfb-select-item-desc">{{ item.desc }}</em>
                  </div>
                </template>
              </ui-select>
            </div>
            <div class="ncfb-of-g" v-if="form.hasOwnProperty('helpIcon')">
              <label class="ncfb-of-l">Help text as icon</label>
              <ui-checkbox id="formSettingsHelpIcon" :checked="form.helpIcon" @change="vchange($event)"
                           prop="helpIcon"></ui-checkbox>
            </div>
            <div class="ncfb-of-g">
              <label class="ncfb-of-l">Submit button</label>
              <input type="text" class="ncfb-of-i" :value="form.submitBtn.text" @input="vchange($event)" prop="submitBtn.text">
            </div>
          </div>
          <div v-show="tab == 'confirmation-options'">
            <h2 class="ncfb-of-h">Confirmation options</h2>

            <h2 class="ncfb-of-h2 is-margin">Submission delivery</h2>
            <p class="ncfb-of-p">Enter e-mail address where submission should be sent to:</p>
            <div class="ncfb-of-g">
              <label class="ncfb-of-l">Email address</label>
              <input type="text" class="ncfb-of-i" :value="get('emailDelivery.emails', [])[0]"
                     @input="onEmailDeliveryEmailChange($event)">
            </div>

            <h2 class="ncfb-of-h2 is-margin">Success action</h2>
            <div class="ncfb-of-g">
              <label class="ncfb-of-l">Type</label>
              <ui-select
                  class="ncfb-of-i"
                  items-width="400px"
                  :items="$options.basicConfirmationValues"
                  id-key="id"
                  name-key="name"
                  :value="form.confirmation.type"
                  prop="confirmation.type"
                  @change="vchange($event)">
                <template slot="items" slot-scope="{ item }">
                  <div class="ncfb-select-item">
                    <span class="ncfb-select-item-name">{{ item.name }}</span>
                    <em v-if="item.desc" class="ncfb-select-item-desc">{{ item.desc }}</em>
                  </div>
                </template>
              </ui-select>
            </div>
            <div class="ncfb-of-g" v-if="form.confirmation.type == 'msg'">
              <label class="ncfb-of-l">Message</label>
              <textarea class="ncfb-of-i" :value="form.confirmation.msg" @input="vchange($event)"
                        prop="confirmation.msg" placeholder="Message"></textarea>
            </div>
            <div class="ncfb-of-g" v-if="form.confirmation.type == 'redirect'">
              <label class="ncfb-of-l">URL address</label>
              <input class="ncfb-of-i" type="text" :value="form.confirmation.url" @input="vchange($event)"
                     prop="confirmation.url" placeholder="URL address">
            </div>
          </div>
          <!-- Custom settings. -->
          <template v-for="(setting, key) in settings__">
            <div v-show="tab == key">
              <h2 class="ncfb-of-h">{{ setting.name }}</h2>
              <component :is="setting.content()"></component>
            </div>
          </template>
          <!-- EO custom settings. -->
        </div>
      </div>
    </template>
  </ncfs-modal>
</template>

<script>
import Vue from 'vue'
import UiIcon from './ui/Icon'
import UiSelect from './ui/Select'
import UiCheckbox from './ui/Checkbox'
import $ from 'cash-dom'

/**
 * Form settings modal.
 */
export default Vue.component('ncfs-form-settings', {
  components: {UiIcon, UiSelect, UiCheckbox},
  props: {
    // Control data passed to control properties editor.
    form: {
      type: Object,
      required: true
    },
    // Resolve if modal is visible or not.
    opened: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // Currently active tab.
      tab: 'basic'
    }
  },
  // The appearance of the label.
  labelPlacementValues: [
    {
      id: 'above',
      name: 'Above',
      desc: 'Label will be place above input.'
    },
    {
      id: 'left',
      name: 'Left',
      desc: 'Label will be place on the left side of the input.'
    },
  ],
  // Types of confirmations.
  basicConfirmationValues: [
    {
      id: 'msg',
      name: 'Show message',
      desc: 'After sending form, message will be displayed.'
    },
    {
      id: 'redirect',
      name: 'Redirect to url',
      desc: 'After sending form, user will be redirected to the specified url address.'
    },
  ],
  methods: {
    get() {
      return this.$root.get(...arguments)
    },
    hasError(input) {
      return input.next().hasClass('ncfb-of-e')
    },
    /**
     * Handles submission.emails input event.
     * @param {String} email
     */
    onEmailDeliveryEmailChange(e) {
      const
          me = this,
          input = $(e.target),
          email = input.val()

      // Remove previous error.
      if (me.hasError(input)) {
        input.next().remove()
      }

      // Validate email address.
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      if (!re.test(String(email).toLowerCase())) {
        if (!me.hasError(input)) {
          input.parent().append('<span class="ncfb-of-e"></span>')
        }
        input.next().text('It is not a valid email address.')
        return
      }

      me.$root.updateForm('emailDelivery.emails', [email])
    },
    vchange(e) {
      let target, value
      if (e.cmp) {
        target = $(e.cmp.$el)
        switch (e.cmp.$options.name) {
          case 'ui-select':
            value = e.value.id
            break
          case 'ui-checkbox':
            value = e.value
            break
        }
      } else {
        target = $(e.target)
        value = target.val()
      }

      const key = target.attr('prop');

      if (target.next().hasClass('ncfb-of-e')) {
        target.next().remove()
      }

      const errors = this.$root.updateForm(key, value)

      if (errors.length) {
        e.preventDefault && e.preventDefault()

        if (!target.next().hasClass('ncfb-of-e')) {
          target.parent().append('<span class="ncfb-of-e"></span>')
        }
        target.next().text(errors[0])
        return
      }
    }
  },
  computed: {
    settings__() {
      return this.$root.settings
    }
  },
  watch: {
    opened(newVal) {
      if (newVal === true) {
        this.$nextTick(() => {
          this.$root.$emit('formSettingsOpen', this);
        })
      }
    }
  }
})
</script>

<style scoped>
@import './../css/options-form.css';

.ncfb-select-item {
  display: block;
  border-bottom: 1px solid #ececec;
  transition: background-color 100ms ease-out;
  cursor: pointer;
  padding-bottom: 13px;
  background: #fafbfc;
  box-shadow: 0 0 33px 0px rgba(0, 0, 0, .1);
}

.ncfb-select-item:hover {
  background-color: #e7f2f5;
}

.ncfb-select-item:hover em {
}

.ncfb-select-item-name {
  padding: 10px 20px;
  font-weight: bold;
  display: block;
  background: #fff;
  margin-bottom: 7px;
}

.ncfb-select-item-desc {
  color: gray;
  padding: 10px 20px 6px 20px;
  display: block;
}
</style>



