<template>
    <div class="ncfb-body">
        <p class="ncfb-value" v-bind:style="{ textAlign: data.align }" @click="$root.openProps(data.uid, { tab: 'basic', focus: 'value' })">
            <span v-text="data.value"></span>
            <ui-icon name="edit" color="#999" width="16" style="margin-bottom: -3px;"></ui-icon>
        </p>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import UiIcon from './../../ui/Icon'
    import { extend, clone } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        components: { UiIcon },
        name: 'fb-paragraph'
    })

    Ctrl.ALIAS = 'paragraph'

    const data = {
        align: 'left',
        value: ''
    }

    const validators = {}

    const info = {
        internal: false,
        desc: '',
        icon: '<svg style="width: 30px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linejoin="round" stroke-miterlimit="1.4" clip-rule="evenodd" viewBox="0 0 15 13"><path fill="#6e6e6e" d="M3 0h11.4v2.6H3z"/><path fill="#6e6e6e" fill-opacity=".7" d="M0 3.4h14.4V6H0zM0 6.8h14.4v2.6H0z"/><path fill="#6e6e6e" fill-opacity=".5" d="M0 10.1h12.6v2.6H0z"/></svg>'
    }

    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Paragraph',
            group: 'text'
        }),
        paragraph: extend(clone(info), {
            name: 'Paragraph',
            data: clone(data),
            validators: validators
        })
    }

    export default Ctrl
</script>
<style scoped>
    .ncfb-body label {
        display: inline-block;
        margin-bottom: .2vw;
        font-size: .8rem;
    }

    .ncfb-value {
        font-size: 14px;
        margin: 10px;
        cursor: pointer;
    }
</style>



