<template>
    <div class="ncfb-ghost">

    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-ghost',
        data() {
            return {
                cell: {
                    // Remove all styles from cell.
                    vapid: true,
                    // Do not render header.
                    header: false,
                    // Do not render bottom dropzone (top dropzone will be enough).
                    dropzoneRenderBottom: false
                }
            }
        }
    })

    Ctrl.ALIAS = 'ghost'
    Ctrl.info = {
        root: {
            internal: true
        },
        ghost: {
            internal: true,
            data: {}
        }
    }

    export default Ctrl
</script>



