<template>
    <div class="ncfb-of-cnt">
        <div class="ncfb-of-n">
            <a :class="{ 'ncfb-of-n-a': tab == 'basic' }" @click="tab = 'basic'">Basic data</a>
            <a :class="{ 'ncfb-of-n-a': tab == 'validation' }" @click="tab = 'validation'">Validation</a>
            <a :class="{ 'ncfb-of-n-a': tab == 'values' }" @click="tab = 'values'">Values</a>
        </div>
        <div class="ncfb-of-f">
            <div v-show="tab == 'basic'">
                <h2 class="ncfb-of-h">Basic data</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Label</label>
                    <input type="text" class="ncfb-of-i" :value="data.label" prop="label" @input="vchange($event)" @blur="setDefault($event)" ref="label">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Help text</label>
                    <input type="text" class="ncfb-of-i" :value="data.help" prop="help" @input="vchange($event)" @blur="setDefault($event)">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Layout</label>
                    <select class="ncfb-of-i" :value="data.layout" prop="layout" @input="vchange($event)">
                        <option value="side-by-side">Side by side</option>
                        <option value="1-column">One column</option>
                        <option value="2-column">Two columns</option>
                        <option value="3-column">Three columns</option>
                    </select>
                </div>
                <div class="ncfb-of-g" v-if="$root.listSetting">
                    <label class="ncfb-of-l">Display on list</label>
                    <ui-checkbox :checked="data.onList" @change="vchange($event)" prop="onList"></ui-checkbox>
                </div>
            </div>
            <div v-show="tab == 'validation'">
                <h2 class="ncfb-of-h">Validation</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Required</label>
                    <ui-checkbox :checked="data.required" @change="vchange($event)" prop="required"></ui-checkbox>
                </div>
            </div>
            <div v-show="tab == 'values'">
                <h2 class="ncfb-of-h">Values</h2>
                <div class="ncfb-of-g ncfb-list">
                    <transition-group name="animated-list">
                        <div v-for="item in data.items" :key="item.uid">
                            <ui-radio @change="$root.findItem(data.uid).item.default = $event.value" :checked="data.default" :value="item.uid" :ncfbhint="checkedHint(item.uid == data.default)"></ui-radio>
                            <input type="text" :value="item.value" @input="updateItem(item.uid, 'value', $event.target.value)">
                            <ui-btn type="list-up" @click="moveItem('up', item.uid)"></ui-btn>
                            <ui-btn type="list-down" @click="moveItem('down', item.uid)"></ui-btn>
                            <ui-btn type="list-rm" @click="removeItem(item.uid)"></ui-btn>
                        </div>
                        <div class="actions" :key="'a'">
                            <ui-btn type="add" text="Add value" @click.native="addItem()"></ui-btn>
                            <ui-btn style="padding: .65vw;" type="reset" text="Reset default" @click.native="resetDefault()"></ui-btn>
                        </div>
                    </transition-group>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * Properties editor for controls type of "textArea".
     */
    import AbstractProps from 'evforms/components/controls/AbstractProps'
    import UiIcon from './../../ui/Icon'
    import UiBtn from './../../ui/Btn'
    import UiRadio from './../../ui/Radio'
    import UiCheckbox from './../../ui/Checkbox'
    import { each } from './../../../util/util'

    export default {
        extends: AbstractProps,
        name: 'fb-radios-props',
        components: { UiIcon, UiBtn, UiRadio, UiCheckbox },
        methods: {
            getItems() {
                return this.$root.findItem(this.data.uid).item.items
            },
            addItem() {
                this.getItems().push({
                    uid: this.$root.findItem(this.data.uid).item.itemsUidIndex++,
                    value: ''
                })
                this.$root.$emit('change', { type: 'control-update' })
            },
            removeItem(uid) {
                const { index } = this.findItem(uid);
                this.getItems().splice(index, 1)
                this.$root.$emit('change', { type: 'control-update' })
            },
            updateItem(uid, key, value) {
                const { item } = this.findItem(uid);
                item[key] = value
                this.$root.$emit('change', { type: 'control-update' })
            },
            moveItem(mode, uid) {
                const { index } = this.findItem(uid);
                const values = this.getItems();

                let newIndex = index;

                // Do nothing if it's first value and mode is up
                // or it's last value and mode is down.
                if (index === 0 && mode == 'up' || index === (values.length - 1) && mode == 'down' ) {
                    return
                }

                mode == 'down' ? newIndex++ : newIndex--

                values.splice(newIndex, 0, values.splice(index, 1)[0])
                this.$root.$emit('change', { type: 'control-update' })
            },
            /**
             * Find item by uid in values.
             * @param uid
             * @returns {*}
             */
            findItem(uid) {
                const result = each(this.getItems(), (item, idx) => {
                    if (item.uid == uid) {
                        return [idx, item]
                    }
                })

                if (result.length) {
                    return { index: result[0][0], item: result[0][1] }
                }

                return {}
            },
            checkedHint(checked) {
                return checked ? 'Checked' : 'Not checked'
            },
            resetDefault() {
                this.$root.findItem(this.data.uid).item.default = null
            }
        }
    }
</script>

<style scoped>
    @import './../../../css/options-form.css';

    .ncfb-list {
        margin-left: 20%;
    }

    .ncfb-list div {
        margin-bottom: 10px;
        transition: all 400ms;
    }

    .ncfb-list input {
        display: inline-block;
        vertical-align: middle;
        border: 1px solid #e6e6e6;
        width: 300px;
        height: 35px;
        padding: 0 10px;
        margin-right: 10px;
    }

    .actions {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin: 30px 0 0 0;
    }

    .actions .ncfb-add {
        margin-right: 10px;
    }

    .animated-list-enter {
        opacity: 0;
        transform: translateX(30px);
    }

    .animated-list-leave-active {
        position: absolute;
    }
</style>



