<template>
    <div class="ncfb-ctrlSelect" @click="controlSelect" @mouseover.stop="$root.addTip('controlSelect', 'Click to add field')" @mouseout.stop="$root.removeTip('controlSelect')">
        <a>Add field</a>
    </div>
</template>

<script>
    import Vue from 'vue'

    const Ctrl = Vue.extend({
        name: 'ControlSelect',
        methods: {
            controlSelect() {
                const items = this.$root.form.items
                this.$root.openControlSelector(items.length ? items[items.length-1].uid : null, 'after')
            }
        }
    })

    export default Ctrl
</script>

<style scoped>
    .ncfb-ctrlSelect {
        background: #8bc34a;
        padding: .8vw;
        /* border: 1px dashed #e6e6e6; */
        border-radius: 32px;
        user-select: none;
        text-align: center;
        cursor: pointer;
        min-width: 100px;
        max-width: 200px;
        margin: 30px auto 20px auto;
        transition: all 250ms ease-out;
        /*border: 6px solid transparent;*/
    }
    .ncfb-ctrlSelect a {
        color: #fff;
    }
    .ncfb-ctrlSelect:hover {
        background: #9edb4f;
    }

</style>



