<template>
    <div class="ncfb-file">
        <label @click="$root.openProps(data.uid, { tab: 'basic', focus: 'label' })">{{ data.label }}</label>
        <button class="ncfb-btn">Wybierz plik</button>
        <span v-if="data.help" class="ncfb-help">{{ data.help }}</span>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import { extend, clone } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-file'
    })

    Ctrl.ALIAS = 'file'
    const data = {
        label: 'Label',
        help: '',
        required: false,
        // Allowed file types (audio, video, image, doc)
        fileTypes: [],
        // Allowed extensions.
        fileExts: [],
        // Allowed mimes.
        fileMimes: [],
        // 2MB
        fileMaxSize: 2000000,
        fileExtUid: 1,
        // Display this field on results list.
        onList: false
    }
    Ctrl.info = {
        root: {
            name: 'File',
            group: 'inputs',
            commonDesc: `
                File upload field.
            `,
            desc: `
                <p>File field allows user to upload file. You may define valid extensions or
                file size and type.</p>
            `,
            icon: `<svg style="width: 22px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 11 13"><g stroke="#6e6e6e"><circle cx="5.1" cy="5.1" r="4.6" fill="#fff"/><path fill="none" d="M2.8 6.2l2.2-3M7.4 6.2l-2.2-3M9.8 11.5l-9.2.2"/></g></svg>`
        },
        file: {
            name: 'File',
            data: extend(clone(data), {
                fileType: 'any'
            }),
            desc: `
                <p>File field allows user to upload file. You may define valid extensions or
                file size and type.</p>
            `,
            icon: `<svg style="width: 22px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 11 13"><g stroke="#6e6e6e"><circle cx="5.1" cy="5.1" r="4.6" fill="#fff"/><path fill="none" d="M2.8 6.2l2.2-3M7.4 6.2l-2.2-3M9.8 11.5l-9.2.2"/></g></svg>`
        },
        image: {
            name: 'Image',
            data: extend(clone(data), {
                fileType: 'image'
            }),
            desc: `
                <p>File field configured to accept only image file.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>Files with extension: JPG, PNG, GIF, BMP, WEBM</strong></span>
            `,
            icon: `<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 28 13"><g stroke="#6e6e6e"><circle cx="5.1" cy="5.1" r="4.6" fill="#fff"/><path fill="none" d="M2.8 6.2l2.2-3M7.4 6.2l-2.2-3M9.8 11.5l-9.2.2"/></g><text x="12.7" y="9.1" fill="#6e6e6e" font-family="'ArialMT','Arial',sans-serif" font-size="8.4">IMG</text></svg>`
        },
        docs: {
            name: 'Document',
            data: extend(clone(data), {
                fileType: 'docs'
            }),
            desc: `
                <p>File field configured to accept only document file.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>Files with extension: DOC/DOCX, ODT, PDF, RTF, TXT, WKS/WPS, WPD</strong></span>
            `,
            icon: `<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 31 13"><g stroke="#6e6e6e"><circle cx="5.1" cy="5.1" r="4.6" fill="#fff"/><path fill="none" d="M2.8 6.2l2.2-3M7.4 6.2l-2.2-3M9.8 11.5l-9.2.2"/></g><text x="12.7" y="9.1" fill="#6e6e6e" font-family="'ArialMT','Arial',sans-serif" font-size="8.4">DOC</text></svg>`
        }
    }

    export default Ctrl
</script>

<style scoped>
    .ncfb-btn {
        background: #bbeaff;
        padding: 8px 12px;
        border-radius: 4px;
        color: #567c9a;
        cursor: pointer;
    }
    .ncfb-file label {
        display: inline-block;
        margin: 10px 20px 10px 0;
    }
    .ncfb-help {
        font-size: 12px;
        display: block;
        font-style: italic;
        color: gray;
    }
</style>



