<template>
    <div class="ncfb-body">
        <label @click="$root.openProps(data.uid, { tab: 'basic', focus: 'label' })">{{ data.label }}</label>
        <span v-if="data.help" class="ncfb-help">{{ data.help }}</span>
        <input type="text" class="ncfb-input" :placeholder="data.placeholder" :value="data.default">
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import { extend, clone } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-input-text',
        watch: {
            // When "validation" changes, update control subtype.
            'data.valueType'(val) {
                if (val === 'any') {
                    val = 'textInput'
                }
                this.$root.updateCtrl(this.data.uid, 'type', this.data.type.split('.')[0] + '.' + val)
            }
        }
    })

    Ctrl.ALIAS = 'textInput'
    const data = {
        label: 'Label',
        help: '',
        placeholder: '',
        required: false,
        default: '',
        // Available values:
        // - any
        // - int
        // - num
        // - decimal
        // - firstName
        // - lastName
        // - email
        // - phone
        // - url
        valueType: 'any',
        valueTypeCfg: {
            any: {
                min: 0,
                max: 9999
            },
            int: {
                min: 0,
                max: 999999999
            },
            num: {
                min: 0,
                max: 999999999
            },
            decimal: {
                min: 0,
                max: 999999999,
                // Minimum number of digits to the right of the decimal point.
                minDec: 1,
                // Maximum number of digits to the right of the decimal point.
                maxDec: 4
            },
            regex: {
                expr: ''
            }
        },
        // Display this field on results list.
        onList: false
    }
    // Common validators.
    const validators = {
        valueTypeCfg: {
            any: {
                min: ['int', ['min', 0], ['max', 9999]],
                max: ['int', ['min', 0], ['max', 9999]]
            },
            int: {
                min: ['int', ['min', 0], ['max', 99999999999999]],
                max: ['int', ['min', 0], ['max', 99999999999999]]
            },
            num: {
                min: ['int', ['min', 0], ['max', 99999999999999]],
                max: ['int', ['min', 0], ['max', 99999999999999]]
            },
            decimal: {
                min: ['int', ['min', 0], ['max', 99999999999999]],
                max: ['int', ['min', 1], ['max', 99999999999999]],
                minDec: ['int', ['min', 1], ['max', 8]],
                maxDec: ['int', ['min', 1], ['max', 8]]
            }
        }
    }
    const info = {
        internal: false,
        desc: '',
        icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="5.9" y="10.3" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">abc</text></svg>'
    }
    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Text input',
            group: 'inputs',
            commonDesc: `
                Single line text input field. Text input may be configured to accepted any value (any letters, numbers or special characters) or value limited to specific characters (like only numbers, only urls or emails).<br>
                To make your life easier below you can find multiple text input fields already preconfigured for you.
            `,
            desc: `
                <p>Single line text input field. Text input can be configured to accepted:</p>
                <ul>
                    <li>Any chars up to 99999</li>
                    <li>First name</li>
                    <li>Last name</li>
                    <li>Integers</li>
                    <li>Numbers</li>
                    <li>Decimals</li>
                    <li>Url</li>
                    <li>E-mail</li>
                </ul>
            `
        }),
        textInput: extend(clone(info), {
            name: 'Text input',
            data: extend(clone(data), {
                valueType: 'any'
            }),
            validators: validators,
            desc: `
                <p>Single line text input field. Text input can be configured to accepted:</p>
                <ul>
                    <li>Any chars up to 99999</li>
                    <li>First name</li>
                    <li>Last name</li>
                    <li>Integers</li>
                    <li>Numbers</li>
                    <li>Decimals</li>
                    <li>Url</li>
                    <li>E-mail</li>
                </ul>
            `
        }),
        firstName: extend(clone(info), {
            name: 'First name',
            data: extend(clone(data), {
                valueType: 'firstName',
                label: 'First name'
            }),
            icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="3.9" y="10.3" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">John</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted only characters allowed to provide "first name".</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>Jack, Kate, Jaś</strong></span>
                <span class="ncfb-not"><span>Not valid: </span><strong>An!na, Ma&lt;th#ew</strong></span>
            `
        }),
        lastName: extend(clone(info), {
            name: 'Last name',
            data: extend(clone(data), {
                valueType: 'lastName',
                label: 'Last name'
            }),
            icon: '<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="6" y="10.3" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">Hall</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted only characters allowed to provide "last name".</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>d'Arras, Luther King, Sausage-Hausen</strong></span>
                <span class="ncfb-not"><span>Not valid: </span><strong>J3n K!owalski, #John</strong></span>
            `
        }),
        email: extend(clone(info), {
            name: 'E-mail',
            data: extend(clone(data), {
                valueType: 'email',
                label: 'E-mail'
            }),
            icon: '<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="3.2" y="9.6" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">me@</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted only email address.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>my@email.com, mail@hashpug.com, test+me@no.realy.domain.com</strong></span>
            `
        }),
        phone: extend(clone(info), {
            name: 'Phone',
            data: extend(clone(data), {
                valueType: 'phone',
                label: 'Phone'
            }),
            icon: '<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="3.2" y="9.6" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">(+48)</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted only phone numbers.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>123456789, (+48) 123456789, +33698912549, +33 6 79 91 25 49</strong></span>
            `
        }),
        zipCode: extend(clone(info), {
            name: 'Zip code',
            data: extend(clone(data), {
                valueType: 'zipCode',
                label: 'Zip code'
            }),
            icon: '<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="3.2" y="9.6" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">78-</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted only zip codes.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>85741, 74-741, 123456789</strong></span>
            `
        }),
        url: extend(clone(info), {
            name: 'Url',
            data: extend(clone(data), {
                valueType: 'url',
                label: 'Url'
            }),
            icon: '<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="2.6" y="9.8" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">http://</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted only valid website url.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>http://domain.com, www.mywebsite.net, other.com:83</strong></span>
            `
        }),
        int: extend(clone(info), {
            name: 'Integers',
            data: extend(clone(data), {
                valueType: 'int'
            }),
            icon: '<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="6" y="10.3" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">123</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted only whole numbers.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>1, 13, 246</strong></span>
                <span class="ncfb-not"><span>Not valid: </span><strong>1.3, 23.54</strong></span>
            `
        }),
        num: extend(clone(info), {
            name: 'Number',
            data: extend(clone(data), {
                valueType: 'num'
            }),
            icon: '<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="7" y="10.4" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">1.1</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted both whole numbers and decimal numbers.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>1, 32, 13.32, 211.3</strong></span>
                <span class="ncfb-not"><span>Not valid: </span><strong>xyz, #89d, lorem-ipsum</strong></span>
            `
        }),
        decimal: extend(clone(info), {
            name: 'Decimal',
            data: extend(clone(data), {
                valueType: 'decimal'
            }),
            icon: '<svg style="width: 40px;" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><text x="6.5" y="10.4" fill="#6e6e6e" font-family="\'ArialMT\',\'Arial\',sans-serif" font-size="8">1.2</text></svg>',
            validators: validators,
            desc: `
                <p>This text input will accepted both whole numbers and decimal numbers.</p>
                <span class="ncfb-valid"><span>Valid: </span><strong>1, 32, 13.32, 211.3</strong></span>
                <span class="ncfb-not"><span>Not valid: </span><strong>xyz, #89d, lorem-ipsum</strong></span>
            `
        })
    }

    export default Ctrl
</script>
<style scoped>
    .ncfb-label {
        width: 80%;
    }
    .ncfb-body label {
        display: inline-block;
        margin: 10px 0;
    }
    .ncfb-help {
        font-size: .8rem;
        display: inline-block;
        font-style: italic;
        color: gray;
        margin-bottom: 8px;
    }
    .ncfb-body label input {
        background: transparent;
        border: 0;
        padding: 0;
        font-size: .8rem;
        outline: none;
    }
    .ncfb-input {
        width: 100%;
        padding: .5vw;
        border: 1px solid #c3c3c3;
        font-size: 1rem;
        transition: all 500ms ease-out;
        outline: none;
        height: 40px;
        margin-bottom: 10px;
    }
    .ncfb-input:focus {
        border-color: #03A9F4;
    }
</style>



