<template>
    <div>
        <div
            v-if="dropzoneRenderTop_"
            :class="{ 'ncfb-dropzone': dropzoneActiveTop_ }"
            class="ncfb-drop"
            :uid="control.uid"
            where="before"
            @click="addCtrl('before')">
            <a>+</a>
            <div></div>
        </div>
        <div
            class="ncfb-ctrl"
            :class="{ 'ncfb-cell': !vapid_ }"
            :style="rootStyle_"
            ref="root">
            <div
                class="ncfb-header"
                :style="headerStyle_"
                v-if="header_">
                <span
                    v-if="ctrlName__"
                    class="ncfb-ctrl-name"
                    @mouseover="$refs.root.classList.add('ncfb-cell-hover')"
                    @mouseout="$refs.root.classList.remove('ncfb-cell-hover')"
                    :class="{ 'ncfb-dragable': dragable_ }"
                    :uid="control.uid">
                        {{ ctrlName__ }}
                </span>
                <div v-if="actions_" class="ncfb-actions">
                    <ui-icon
                        name="settings"
                        width="14"
                        color="#808080"
                        hint="Settings"
                        @click.native="$root.openProps(control.uid)"
                        v-if="$root.getCtrlDef(control.type).props"></ui-icon>
                    <ui-icon
                        name="copy"
                        width="14"
                        color="#808080"
                        hint="Duplicate"
                        @click.native="$root.cloneCtrl(control.uid)"
                        v-if="control.type !== 'cnt.cnt'"></ui-icon>
                    <ui-icon
                        name="close"
                        width="14"
                        color="#808080"
                        hint="Remove"
                        @click.native="removeCtrl($event.shiftKey)"
                        @mouseover="$root.addTip('cell.remove', 'Click to remove control | SHIFT + click to remove without notice.')"
                        mouseout="$root.removeTip('cell.remove')"></ui-icon>
                    <slot name="actions"></slot>
                </div>
            </div>
            <div :class="{ 'ncfb-ctrl-cnt': !vapid_ }" :style="cntStyle_">
                <component
                        :is="$root.getCtrlDef(control.type).cmp"
                        :data="control"
                        @configure="configureCell"
                ></component>
            </div>
        </div>
        <div
            v-if="dropzoneRenderBottom_"
            :class="{ 'ncfb-dropzone': dropzoneActiveBottom_ }"
            class="ncfb-drop"
            :uid="control.uid"
            where="after"
            @click="addCtrl('after')">
            <a>+</a>
            <div></div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import PropsModal from './../ControlPropsModal'
    import UiIcon from './../ui/Icon'

    export default Vue.extend({
        name: 'fb-cell',
        components: { UiIcon },
        props: {
            // Control data.
            control: {
                type: Object,
                required: true
            },
            index: {
                type: Number,
                required: true
            },
            // Can this cell be dragged (reordered?).
            dragable: {
                type: Boolean,
                default: true
            },
            actions: {
                type: Boolean,
                default: true
            },
            // Render dropzone at top.
            dropzoneRenderTop: {
                type: Boolean,
                default: true
            },
            // Activate top dropzone.
            dropzoneActiveTop: {
                type: Boolean,
                default: true
            },
            dropzoneRenderBottom: {
                type: Boolean,
                default: true
            },
            dropzoneActiveBottom: {
                type: Boolean,
                default: true
            },
            // Should cell have its styles?
            vapid: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                actions_: this.actions,
                dragable_: this.dragable,
                dropzoneRenderTop_: this.dropzoneRenderTop,
                dropzoneActiveTop_: this.dropzoneActiveTop,
                dropzoneRenderBottom_: this.dropzoneRenderBottom,
                dropzoneActiveBottom_: this.dropzoneActiveBottom,
                vapid_: this.vapid,
                propsOpened_: false,
                rootStyle_: {},
                headerStyle_: {},
                cntStyle_: {},
                // Render or not header.
                header_: true
            }
        },
        watch: {
            dropzoneRenderTop(val) {
                this.dropzoneRenderTop_ = val
            },
            dropzoneRenderBottom(val) {
                this.dropzoneRenderBottom_ = val
            }
        },
        mounted() {
            this.$root.cells[this.control.uid] = {
                data: this.control,
                el: this.$el
            }
        },
        beforeDestroy() {
            delete this.$root.cells[this.control.uid]
        },
        methods: {
            addCtrl(insertMode) {
                this.$root.openControlSelector(this.control.uid, insertMode)
            },
            openProps() {
                const me = this,
                    $root = me.$root,
                    props = $root.props,
                    control = me.control,
                    type = control.type;

                if (!props[type]) {
                    const modal = new PropsModal({
                        propsData: {
                            control
                        },
                        parent: me
                    }).$mount()
                    $root.$el.firstChild.appendChild(modal.$el)
                    modal.open()
                    props[type] = modal
                } else {
                    console.log(props[type])
                    Vue.set(props[type], 'control', control)
                    console.log('yes')
                    //props[type].control = control
                }

            },
            closeProps() { this.propsOpened_ = false; },
            configureCell(ctrl) {
                const me = this;

                if (ctrl.hasOwnProperty('cell')) {
                    const cell = ctrl.cell;

                    const has = (key) => {
                        return cell.hasOwnProperty(key)
                    }

                    if (has('actions')) {
                        me.actions_ = cell.actions
                    }
                    if (has('dragable')) {
                        me.dragable_ = cell.dragable
                    }
                    if (has('dropzoneRenderTop')) {
                        me.dropzoneRenderTop_ = cell.dropzoneRenderTop
                    }
                    if (has('dropzoneActiveTop')) {
                        me.dropzoneActiveTop_ = cell.dropzoneActiveTop
                    }
                    if (has('dropzoneRenderBottom')) {
                        me.dropzoneRenderBottom_ = cell.dropzoneRenderBottom
                    }
                    if (has('dropzoneActiveBottom')) {
                        me.dropzoneActiveBottom_ = cell.dropzoneActiveBottom
                    }
                    if (has('vapid')) {
                        me.vapid_ = cell.vapid
                    }
                    if (has('propsCmp')) {
                        me.propsCmp = cell.propsCmp
                    }
                    if (has('rootStyle')) {
                        me.rootStyle_ = cell.rootStyle
                    }
                    if (has('headerStyle')) {
                        me.headerStyle_ = cell.headerStyle
                    }
                    if (has('cntStyle')) {
                        me.cntStyle_ = cell.cntStyle
                    }
                    if (has('header')) {
                        me.header_ = cell.header
                    }
                }
            },
            removeCtrl(silent) {
                const
                    me = this,
                    $root = me.$root;

                $root.removeTip('cell.remove')

                if (silent) {
                    $root.removeCtrl(me.control.uid)
                    return
                }
                if (confirm('Are you sure you want to remove this control?')) {
                    $root.removeCtrl(me.control.uid)
                }
            }
        },
        computed: {
            /**
             * Return name of the control.
             * @returns {*}
             * @private
             */
            ctrlName__() {
                const type = this.control.type
                return this.$root.getCtrlDef(type).info.name
            }
        }
    })
</script>

<style scoped>

    .ncfb-cell {
        background: #fff;
        /*padding: 1vw;*/
        border: 1px dashed transparent;
        border-top: 0;
        border-radius: 6px;
        user-select: none;
        -moz-user-select: -moz-none;
        transition: all 100ms ease-out;
        box-shadow: 0px 0px 4px -1px rgba(152, 152, 152, 0.6);
    }
    .ncfb-cell-hover {
        border: 1px solid #88A7F1;
        border-top: 0;
    }
    .ncfb-drop {
        width: 100%;
        height: 20px;
        position: relative;
    }
    .ncfb-drop > div {
        position: absolute;
        width: 100%;
        height: 1px;
        top: 10px;
        left: 0;
    }
    .ncfb-drop.ncfb-drop-ready > div {
        background: #a8d4ef;
        box-shadow: 0px 0px 10px 2px rgba(97, 168, 255, 0.5);
        transition: all 200ms ease-out;
    }
    .ncfb-drop:not(.ncfb-drop-ready) {
        cursor: pointer;
    }
    .ncfb-drop:hover:not(.ncfb-drop-ready) > div {
        background: #8bc34a;
        transition: all 200ms ease-out;
    }
    .ncfb-drop > a {
        display: none;
        opacity: 0;
        transition: all 250ms ease-out;
        transform: translateX(10px);
        background: #8bc34a;
        display: block;
        position: absolute;
        left: -10px;
        top: 0;
        color: #fff;
        z-index: 1100;
        border-radius: 40px;
        width: 20px;
        height: 20px;
        text-align: center;
        font-size: 0.8em;
        line-height: 19px;
    }
    .ncfb-drop:hover:not(.ncfb-drop-ready) > a {
        transform: translateX(0);
        opacity: 1;
    }
    .ncfb-header {
        width: 100%;
        position: relative;
        border-top: 1px solid #e6e6e6;
        text-align: left;
        border-radius: 6px 6px 0 0;
        background: linear-gradient(to right, #fafbfc 80%,#ffffff 100%);
    }
    .ncfb-cell-hover > .ncfb-header {
        border-top-color: #88A7F1;
    }
    .ncfb-ctrl-name {
        background: #e6e6e6;
        padding: .4vh 1vh;
        font-size: 10px;
        text-transform: uppercase;
        display: inline-block;
        line-height: 15px;
        color: gray;
        cursor: pointer;
        border: 1px solid #e6e6e6;
        margin: -1px 0 0 -1px;
        border-radius: 6px 0 0 0;
        cursor: move;
        vertical-align: top;
    }
    .ncfb-cell-hover > .ncfb-header .ncfb-ctrl-name {
        border-color: #88A7F1;
        background: #88A7F1;
        color: #fff;
    }

    /*
        CELL TOP ACTIONS.
        ----
        Mostly two icons for delete and open properties.
    */
    .ncfb-actions {
        position: absolute;
        top: 0;
        right: 0;
        padding: 0 6px;
        color: gray;
    }
    .ncfb-actions:hover {
        opacity: 1;
    }
    .ncfb-actions > span {
        padding: 10px 3px;
        cursor: pointer;
    }
    .ncfb-actions > span:hover {
        background: #88a7f1;
    }
    .ncfb-actions > span:hover >>> svg {
        cursor: pointer;
    }
    .ncfb-actions > span:hover >>> path {
        fill: #fff;
        cursor: pointer;
    }
    .ncfb-cell-hover > .ncfb-header .ncfb-actions {
        border-radius: 0 6px 0 0;
    }
    /* EO CELL TOP ACTIONS. */

    .ncfb-ctrl-cnt {
        padding: 10px 20px;
    }

    .ncfb-ctrl {
        position: relative;
        text-align: left;
    }
    .ncfb-ctrl.ncfb-drop-ready .ncfb-drop-mask {
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(97, 168, 255, 0.6);
        width: 100%;
        height: 100%;
        text-align: center;
        display: block;
    }
    .ncfb-ctrl.ncfb-drop-ready .ncfb-drop-mask span {
        color: #fff;
        padding-top: 40px;
        display: block;
    }
    .ncfb-drop-mask {
        display: none;
    }

    .ncfb-props-opened {
        display: block !important;
    }
</style>



