<template>
    <div class="ncfb-of-cnt">
        <div class="ncfb-of-n">
            <a :class="{ 'ncfb-of-n-a': tab == 'basic' }" @click="tab = 'basic'">Basic data</a>
            <a :class="{ 'ncfb-of-n-a': tab == 'validation' }" @click="tab = 'validation'">Validation</a>
        </div>
        <div class="ncfb-of-f">
            <div v-show="tab == 'basic'">
                <h2 class="ncfb-of-h">Basic data</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Label</label>
                    <input type="text" class="ncfb-of-i" :value="data.label" prop="label" @input="vchange($event)" @blur="setDefault($event)" ref="label">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Help text</label>
                    <input type="text" class="ncfb-of-i" :value="data.help" prop="help" @input="vchange($event)" @blur="setDefault($event)">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Placeholder</label>
                    <input type="text" class="ncfb-of-i" :value="data.placeholder" prop="placeholder" @input="vchange($event)" @blur="setDefault($event)">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Default value</label>
                    <input type="text" class="ncfb-of-i" :value="data.default" prop="default" @input="vchange($event)" @blur="setDefault($event)">
                </div>
                <div class="ncfb-of-g" v-if="$root.listSetting">
                    <label class="ncfb-of-l">Display on list</label>
                    <ui-checkbox :checked="data.onList" @change="vchange($event)" prop="onList"></ui-checkbox>
                </div>
            </div>
            <div v-show="tab == 'validation'">
                <h2 class="ncfb-of-h">Validation</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Required</label>
                    <ui-checkbox :checked="data.required" @change="vchange($event)" prop="required"></ui-checkbox>
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Type of value</label>
                    <ui-select
                            class="ncfb-of-i"
                            items-width="400px"
                            :items="$options.valueTypes"
                            id-key="id"
                            name-key="name"
                            :value="data.valueType"
                            @change="$root.updateCtrl(data.uid, 'valueType', $event.value.id)">
                        <template slot="items" slot-scope="{ item }">
                            <div class="ncfb-select-item">
                                <span class="ncfb-select-item-name">{{ item.name }}</span>
                                <em v-if="item.desc" class="ncfb-select-item-desc">{{ item.desc }}</em>
                                <span v-if="item.valid" class="ncfb-ok"><strong>Valid:</strong> <span v-html="item.valid"></span></span>
                                <span v-if="item.not" class="ncfb-not"><strong>Not valid:</strong> <span v-html="item.not"></span></span>
                            </div>
                        </template>
                    </ui-select>
                </div>
                <div class="ncfb-of-g">
                    <div class="ncfb-of-desc">
                        <p>{{ extDesc }}</p>
                        <span v-if="valid" class="ncfb-ok"><strong>Valid:</strong> <span v-html="valid"></span></span>
                        <span v-if="notValid" class="ncfb-not"><strong>Not valid:</strong> <span v-html="notValid"></span></span>
                    </div>
                </div>
                <div v-if="data.valueType == 'any'">
                    <div class="ncfb-of-g2">
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="Minimum number of characters accepted by field"></fb-icon>
                            <label class="ncfb-of-l">Min</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.any.min" prop="valueTypeCfg.any.min" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="Maximum number of characters accepted by field"></fb-icon>
                            <label class="ncfb-of-l">Max</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.any.max" prop="valueTypeCfg.any.max" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                    </div>
                </div>
                <div v-if="data.valueType == 'int'">
                    <div class="ncfb-of-g2">
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="The lowest number accepted by field"></fb-icon>
                            <label class="ncfb-of-l">Min</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.int.min" prop="valueTypeCfg.int.min" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="The highest number accepted by field"></fb-icon>
                            <label class="ncfb-of-l">Max</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.int.max" prop="valueTypeCfg.int.max" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                    </div>
                </div>
                <div v-if="data.valueType == 'num'">
                    <div class="ncfb-of-g2">
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="The lowest number accepted by field"></fb-icon>
                            <label class="ncfb-of-l">Min</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.num.min" prop="valueTypeCfg.num.min" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="The highest number accepted by field"></fb-icon>
                            <label class="ncfb-of-l">Max</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.num.max" prop="valueTypeCfg.num.max" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                    </div>
                </div>
                <div v-if="data.valueType == 'decimal'">
                    <div class="ncfb-of-g2">
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="The lowest number accepted by field"></fb-icon>
                            <label class="ncfb-of-l">Min</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.decimal.min" prop="valueTypeCfg.decimal.min" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="The highest number accepted by field"></fb-icon>
                            <label class="ncfb-of-l">Max</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.decimal.max" prop="valueTypeCfg.decimal.max" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                    </div>
                    <div class="ncfb-of-g2">
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="Minimum number of digits to the right of the decimal point"></fb-icon>
                            <label class="ncfb-of-l">Min. digits</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.decimal.minDec" prop="valueTypeCfg.decimal.minDec" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                        <div class="ncfb-of-g">
                            <fb-icon name="help" class="ncfb-of-hp" hint="Maximum number of digits to the right of the decimal point"></fb-icon>
                            <label class="ncfb-of-l">Max. digits</label>
                            <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.decimal.maxDec" prop="valueTypeCfg.decimal.maxDec" @input="vchange($event)" @blur="setDefault($event)">
                        </div>
                    </div>
                </div>
                <div v-if="data.valueType == 'regex'">
                    <div class="ncfb-of-g">
                        <fb-icon name="help" class="ncfb-of-hp" hint="Regular expression eg. [a-z]+"></fb-icon>
                        <label class="ncfb-of-l">Expression</label>
                        <input type="text" class="ncfb-of-i" :value="data.valueTypeCfg.regex.min" @change="vchange('valueTypeCfg.regex.expr', $event)">
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * Properties editor for controls type of "textInput".
     */
    import AbstractProps from 'evforms/components/controls/AbstractProps'
    import FbIcon from './../../ui/Icon'
    import UiCheckbox from './../../ui/Checkbox'
    import UiSelect from './../../ui/Select'
    import { each } from './../../../util/util'

    export default {
        extends: AbstractProps,
        name: 'fb-text-input-props',
        components: { FbIcon, UiCheckbox, UiSelect },
        valueTypes: [
            {
                id: 'any',
                name: 'Any value',
                desc: 'Any value provided by user will be accepted.',
                extDesc: 'Any provided value is accepted.'
            },
            {
                id: 'int',
                name: 'Integer',
                desc: 'Whole numbers',
                extDesc: 'User will be able to enter only whole numbers.',
                valid: '1, 13, 246',
                not: '1.3, 23.54'
            },
            {
                id: 'num',
                name: 'Number',
                desc: 'Any integer or decimal.',
                extDesc: 'User will be able to enter any whole or decimal number.',
                valid: '1, 32, 13.32, 211.3'
            },
            {
                id: 'decimal',
                name: 'Decimal',
                desc: 'Only decimal numbers.',
                extDesc: 'User will be able to enter only decimal number.',
                valid: '1.32, 64.2 130.32',
                not: '1, 32, 423'
            },
            {
                id: 'firstName',
                name: 'First name',
                desc: 'Accepted are only letters without spaces and special characters.',
                valid: 'Jack, Kate, Jaś',
                not: 'An!na, Ma&lt;th#ew'
            },
            {
                id: 'lastName',
                name: 'Last name',
                desc: 'Accepted are only letters without special characters except dashes and spaces.',
                valid: 'Bloemhof, Schweitzer II, Wruck-Dutra, Płaszczyński',
                not: 'Tu3ek, Lev-#tt, 3ak_ovo%'
            },
            {
                id: 'url',
                name: 'Url address',
                desc: 'Url address',
                valid: 'http://domain.com, mypage.com'
            },
            {
                id: 'email',
                name: 'E-mail address',
                desc: 'E-mail address',
                valid: 'john@kowalski.com, me@gmail.com, hello@aol.com'
            },
            {
                id: 'phone',
                name: 'Phone number',
                desc: 'Only phone numbers are accepted.',
                valid: '123456789, (+48) 123456789, +33698912549, +33 6 79 91 25 49'
            },
            {
                id: 'zipCode',
                name: 'Zip code',
                desc: 'Only postal codes are accepted.',
                valid: '85741, 74-741, 123456789'
            }
        ],
        methods: {
            valueType(id) {
                return this.valueTypesMap[id]
            }
        },
        computed: {
            valid() {
                return this.valueTypesMap[this.data.valueType].valid
            },
            notValid() {
                return this.valueTypesMap[this.data.valueType].not
            },
            extDesc() {
                const type = this.valueTypesMap[this.data.valueType]
                return type.extDesc || type.desc
            }
        },
        created() {
            this.valueTypesMap = {}
            each(this.$options.valueTypes, (type) => {
                this.valueTypesMap[type.id] = type
            })
        }
    }
</script>

<style scoped>
    @import './../../../css/options-form.css';

    .ncfb-of-desc span {
        padding-left: 0;
        width: auto;
        display: inline-block;
    }


    .ncfb-select-item {
        display: block;
        border-bottom: 1px solid #ececec;
        transition: background-color 100ms ease-out;
        cursor: pointer;
        padding-bottom: 13px;
        background: #fafbfc;
        box-shadow: 0 0 33px 0px rgba(0, 0, 0, .1);
    }
    .ncfb-select-item:hover {
        background-color: #e7f2f5;
    }
    .ncfb-select-item:hover em {
    }
    .ncfb-select-item-name {
        padding: 10px 20px;
        font-weight: bold;
        display: block;
        background: #fff;
        margin-bottom: 7px;
    }
    .ncfb-select-item-desc {
        color: gray;
        padding: 10px 20px 6px 20px;
        display: block;
    }
    .ncfb-ok {
        display: block;
        color: gray;
        padding: 6px 20px 4px 20px;
    }
    .ncfb-ok strong {
        font-weight: normal;
        color: #10c506;
    }
    .ncfb-not {
        display: block;
        color: gray;
        padding: 4px 20px 6px 20px;
    }
    .ncfb-not strong {
        font-weight: normal;
        color: #da2764;
    }
</style>



