<template>
    <div class="ncfb-ctrl ncfb-file" :class="{ 'ncfb-ctrl-error': error }">
        <component :is="cmpLbl" :label="data.label" :help="data.help"></component>
        <button class="ncfb-btn" @click="selectFile">Wybierz plik</button>
        <span class="ncfb-file-name" v-show="file">Selected file: <strong>{{ file }}</strong></span>
        <input type="file" ref="file" @change="updatePreview">
        <span v-if="data.help && $root.form.lblPos === 'left'" class="ncfb-help">{{ data.help }}</span>
        <span class="ncfb-error" v-show="error">{{ error }}</span>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../../renderer/AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-input-text',
        data() {
            return {
                file: ''
            }
        },
        methods: {
            getValue() {
                return this.$refs.file.files
            },
            updatePreview() {
                const
                    input = this.$refs.file,
                    files = input.files;

                if (files && files[0]) {
                    const file = files[0]

                    this.file = file.name

                    // const reader = new FileReader()
                    // reader.onload = (e) => {
                    //     console.log(e)
                    // }
                    // reader.readAsDataURL(files[0])
                }
            },
            selectFile() {
                this.error = false
                this.$refs.file.click()
            }
        }
    })

    Ctrl.ALIAS = 'file'
    export default Ctrl
</script>
<style scoped>
    .ncfb-file input[type=file] {
        position: absolute;
        left: -9999em;
    }
</style>



