<template>
  <div :class="[theme__, lblPosCss, 'ncfb']">
    <div class="ncfb-msg" ref="fakeMsg" style="position: absolute; left: -99999px;">
      <div class="ncfb-msg-body">
        {{ form_.confirmation.msg }}
      </div>
    </div>
    <transition name="no-mode-translate-fade" mode="out-in">
      <div class="ncfb-msg" v-if="successMsg_" key="msg" ref="msg" :style="{ marginTop: msgHeight_ / 2 * -1 + 'px' }">
        <div class="ncfb-msg-body">
          {{ form_.confirmation.msg }}
        </div>
      </div>
      <div class="ncfb-form" v-else key="form">
        <div
            v-for="(item, index) in form_.items"
            :key="item.uid">
          <fb-cell
              v-if="hasCtrlDef(item.type)"
              :index="index"
              :control="item"
              ref="cells"></fb-cell>
        </div>
        <div class="ncfb-form-btn">
          <button type="button" @click="submit" :disabled="!url || sending">{{ form_.submitBtn.text }}</button>
          <ui-icon v-show="sending" name="loader" color="#999" width="15" style="margin-left: 5px"></ui-icon>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import Vue from 'vue'
  import $ from 'cash-dom'
  import UiIcon from './ui/Icon'

  import {each, extend, isFn, isObj, clone} from './../util/util'
  import normalizeFormDef from "../util/normalize-form-definition";
  // Controls.
  import FbCell from './cell/FormCtrl.vue'
  import TextInput from './controls/textInput/FormCtrl'
  import TextArea from './controls/textArea/FormCtrl'
  import Select from './controls/select/FormCtrl'
  import Heading from './controls/heading/FormCtrl'
  import Paragraph from './controls/paragraph/FormCtrl'
  import Separator from './controls/separator/FormCtrl'
  import Container from './controls/cnt/FormCtrl'
  import Checkbox from './controls/checkbox/FormCtrl'
  import Radio from './controls/radio/FormCtrl'
  import File from './controls/file/FormCtrl'

  export default Vue.extend({
    name: 'FbForm',
    props: {
      form: {
        type: Object,
        required: true
      },
      /**
       * @property {Function} onSubmit
       * Function called when user clicks on forms "submit" button. If this config
       * is not specified ajax request will be immediatly maded. If this config
       * is specified, provided function will be called instead.
       *
       * ```js
       * hspg.form.renderer.create({
       *    el: '#form',
       *    url: '/form_handler.php',
       *    themes: {
       *        'ncfbdef': { name: 'Default', url: 'assets/form-default.css' }
       *    },
       *    onSubmit() {
       *        const form = this
       *
       *        // Do some interesting stuff here.
       *
       *        // Send form manually (form will make ajax request now).
       *        form.send()
       *    }
       * })
       * ```
       */
      onSubmit: {
        type: Function,
        default: null
      },
      /**
       * @property {Object} [requestData={}]
       * Additional data passed in ajax request when submitting form.
       */
      requestData: {
        type: Object,
        default: () => {
          return {}
        }
      },
      /**
       * Available themes.
       * Format:
       *
       *     new Form({
       *         propsData: {
       *             themes: {
       *                 ncfbdev: '/url/to/default.css'
       *             }
       *         }
       *     })
       *
       * @property object [themes={}]
       */
      themes: {
        type: Object,
        default: () => {
          return {};
        }
      },
      theme: {
        type: String
      },
      /**
       * Save url.
       */
      // It's not required. If url is not specified send button is disabled (it's needed
      // in builders preview mode).
      url: {
        type: String
      }
    },
    components: {FbCell, UiIcon},
    controls: {
      textInput: {
        items: {
          textInput: {
            cmp: TextInput
          },
          firstName: {
            cmp: TextInput
          },
          lastName: {
            cmp: TextInput
          },
          email: {
            cmp: TextInput
          },
          phone: {
            cmp: TextInput
          },
          zipCode: {
            cmp: TextInput
          },
          url: {
            cmp: TextInput
          },
          int: {
            cmp: TextInput
          },
          num: {
            cmp: TextInput
          },
          decimal: {
            cmp: TextInput
          }
        }
      },
      textArea: {
        items: {
          textArea: {
            cmp: TextArea
          }
        },
      },
      select: {
        items: {
          select: {
            cmp: Select
          }
        },
      },
      heading: {
        items: {
          heading: {
            cmp: Heading
          },
          heading2: {
            cmp: Heading
          },
          heading3: {
            cmp: Heading
          },
          heading4: {
            cmp: Heading
          }
        }
      },
      paragraph: {
        items: {
          paragraph: {
            cmp: Paragraph
          }
        }
      },
      separator: {
        items: {
          separator: {
            cmp: Separator
          }
        }
      },
      cnt: {
        items: {
          cnt: {
            cmp: Container
          },
          two: {
            cmp: Container
          },
          three: {
            cmp: Container
          },
          four: {
            cmp: Container
          }
        }
      },
      checkbox: {
        items: {
          checkbox: {
            cmp: Checkbox
          }
        }
      },
      radio: {
        items: {
          radio: {
            cmp: Radio
          }
        }
      },
      file: {
        items: {
            file: {
                cmp: File
            },
            image: {
                cmp: File
            },
            docs: {
                cmp: File
            }
        }
      }
    },
    data() {
      return {
        theme_: this.theme,
        successMsg_: false,
        msgHeight_: 0,
        sending: false,
        // It will be immediately changed by "form" properties "watch".
        form_: {},
      }
    },
    methods: {
      getCtrlDef(alias, returnNull = false) {
        const cache = this.cache
        if (cache[alias]) {
          return cache[alias]
        }

        if (alias.indexOf('.') === -1) {
          const control = this.$options.controls[alias]
          if (!control) {
            if (returnNull) {
              return null
            }
            throw new TypeError(`Control "${alias}" can't be found.`)
          }
          return cache[alias] = control
        }

        const parts = alias.split('.');
        const parent = this.$options.controls[parts[0]]

        if (!parent) {
          if (returnNull) {
            return null
          }
          throw new TypeError(`Control "${alias}" can't be found ("${parts[0]}").`)
        }

        const control = parent.items[parts[1]]

        if (!control) {
          if (returnNull) {
            return null
          }
          throw new TypeError(`Control "${alias}" can't be found ("${parts[0]}").`)
        }

        return cache[alias] = control
      },
      hasCtrlDef(alias) {
        return this.getCtrlDef(alias, true) !== null
      },
      submit() {
        const me = this

        this.sending = true

        if (isFn(me.onSubmit)) {
          me.onSubmit.call(this)
        } else {
          this.send()
        }
      },
      /**
       * Sends form.
       *
       * @param {Object} reqData Additional data passed to ajax request.
       */
      send(reqData) {
        const me = this,
            // All values from controls.
            values = {},
            data = {};

        // Clear previous errors.
        each(me.controls, (ctrl) => {
          ctrl.error = false
        })

        // Collect all data from controls.
        each(me.$refs.cells, (cell) => {
          const value = cell.value()
          if (value !== null) {
            extend(values, cell.value())
          }
        })

        const formData = new FormData()

        each(values, (value, key) => {
          if (value instanceof FileList && value.length > 0) {
            formData.append(key, value[0])
          } else {
            data[key] = value
          }
        })

        const el = me.$el

        // Set height for css animation.
        //el.style.height = el.offsetHeight + 'px';

        formData.append('_fc', JSON.stringify(data))

        // Append additional request data.
        each(extend(clone(me.requestData), isObj(reqData) ? reqData : {}), (value, key) => formData.append(key, value))

        const xhr = new XMLHttpRequest();
        xhr.open('POST', this.url);

        xhr.onerror = function () {
          console.log(this.statusText, this)
          //me.uploadError = this.statusText || 'upload failed'
        };

        xhr.onload = function () {
          if (this.status !== 200) {
            if (this.status === 400) {
              const errors = JSON.parse(this.response).errors
              if (isObj(errors)) {
                each(errors, (error, uid) => {
                  const ctrl = me.controls[uid]
                  if (ctrl) {
                    ctrl.error = error
                  }
                })

                // Scroll to the first validation error.
                const el = me.$el.getElementsByClassName('ncfb-error')[0]
                window.scrollTo(0, el.getBoundingClientRect().top)
                me.sending = false
              }
            } else {

            }
          } else {
            const conf = me.form.confirmation
            switch (conf.type) {
              case 'msg':
                // Show success message.
                me.successMsg_ = true
                el.style.height = el.offsetHeight + 'px'
                setTimeout(() => {
                  el.style.height = me.msgHeight_ + 60 + 'px'
                }, 50)

                me.sending = false

                break;
              case 'redirect':
                me.sending = false

                window.location = conf.url
                break;
            }
          }
        }

        xhr.upload.onprogress = function (event) {
          // if (event.lengthComputable) {
          //     me.progress = (event.loaded / event.total * 100).toFixed(2)
          // }
        }

        xhr.send(formData);
      },
      /**
       * @method useTheme
       * @param string uid Theme uid to use.
       * @return void
       */
      useTheme(uid) {
        const me = this,
            theme = me.themes[uid];

        if (!theme) {
          throw new Error(`Theme ${uid} is not defined.`)
        }

        if (!$(`#ncfb-theme-${uid}`).length) {
          $(`<link rel="stylesheet" href="${theme.url}" id="ncfb-theme-${uid}">`).appendTo('head')
        }

        this.theme_ = uid
      }
    },
    beforeCreate() {
      // Controls definitions cache.
      this.cache = {}
      // Object with all controls. Every control register here itself on creation.
      this.controls = {}
    },
    mounted() {
      this.useTheme(this.theme__)

      // Get height of the success message.
      setTimeout(() => {
        const fakeMsg = this.$refs.fakeMsg
        this.msgHeight_ = fakeMsg.offsetHeight
        // Remove fake message form DOM, we don't need it anymore.
        fakeMsg.parentNode.removeChild(fakeMsg)
      }, 1000);
    },
    computed: {
      /**
       * Return css class name for label position.
       * @return string
       * @computed
       */
      lblPosCss() {
        return 'ncfb-lbl-' + this.form.lblPos
      },
      /**
       * Return theme name.
       * @returns string
       * @computed
       */
      theme__() {
        if (!this.theme_) {
          this.theme_ = this.form.theme
        }
        return this.theme_
      }
    },
    watch: {
      // Watch "form" property and apply defaults to it.
      form: {
        // Call it as soon as component is created.
        immediate: true,
        deep: true,
        handler(form) {
          this.form_ = normalizeFormDef(form)
        }
      }
    }
  })
</script>

<style scoped>
@import '../css/reset.css';

.ncfb {
  position: relative;
  transition: all 1s ease-out;
  overflow: hidden;
  padding: 4px;
}

.no-mode-translate-fade-enter-active, .no-mode-translate-fade-leave-active {
  transition: all 1s;
}

.no-mode-translate-fade-enter, .no-mode-translate-fade-leave-active {
  opacity: 0;
}

.no-mode-translate-fade-enter {
  transform: translateX(31px);
}

.no-mode-translate-fade-leave-active {
  transform: translateX(-31px);
}
</style>



