<template>
  <fb-builder :form="form" ref="builder"></fb-builder>
</template>

<script>
import Vue from 'vue'
import FbBuilder from './Builder.vue'
import $ from 'cash-dom'
import {isFn, isArr, extend, each, clone, set, get, tpl, uuidv4} from './util/util'
import normalizeFormDef from "./util/normalize-form-definition";
import * as validators from './util/validators'

import FbPreviewModal from './components/PreviewModal'

// Components.
// ----------------------------------------------------------------------------
import './components/ui/Modal'
import './components/ControlSelectorModal'
import './components/FormSettings'
import './components/ControlPropsModal'

// CONTROLS
// ----------------------------------------------------------------------------
// TextInput
import TextInputControl from './components/controls/textInput/BuilderCtrl'
import TextInputProps from './components/controls/textInput/Props'
// TextArea
import TextAreaControl from './components/controls/textArea/BuilderCtrl'
import TextAreaProps from './components/controls/textArea/Props'
// Select
import SelectControl from './components/controls/select/BuilderCtrl'
import SelectProps from './components/controls/select/Props'
// Heading
import HeadingControl from './components/controls/heading/BuilderCtrl'
import HeadingProps from './components/controls/heading/Props'
// Paragraph
import ParagraphControl from './components/controls/paragraph/BuilderCtrl'
import ParagraphProps from './components/controls/paragraph/Props'
// Separator
import SeparatorControl from './components/controls/separator/BuilderCtrl'
// Container.
import ContainerControl from './components/controls/cnt/BuilderCtrl'
import ContainerProps from './components/controls/cnt/Props'
// Checkbox.
import CheckboxControl from './components/controls/checkbox/BuilderCtrl'
import CheckboxProps from './components/controls/checkbox/Props'
// Radio.
import RadioControl from './components/controls/radio/BuilderCtrl'
import RadioProps from './components/controls/radio/Props'
// Ghost.
import GhostControl from './components/controls/ghost/BuilderCtrl'
// File.
import FileControl from './components/controls/file/BuilderCtrl'
import FileProps from './components/controls/file/Props'
// EO CONTROLS
// ----------------------------------------------------------------------------

const controls = {}

function register(control, props = null) {
  const alias = control.ALIAS
  each(control.info, (info, key) => {
    controls[alias + '.' + key] = {
      cmp: control,
      props: control.props || props,
      info: info
    }
  })
}

register(ContainerControl, ContainerProps)
register(TextInputControl, TextInputProps)
register(TextAreaControl, TextAreaProps)
register(SelectControl, SelectProps)
register(HeadingControl, HeadingProps)
register(ParagraphControl, ParagraphProps)
register(SeparatorControl)
register(CheckboxControl, CheckboxProps)
register(RadioControl, RadioProps)
register(GhostControl)
register(FileControl, FileProps)

const formValidators = {
  lblPos: [['oneof', ['above', 'left']]],
  captcha: ['bool']
}

export default Vue.extend({
  components: {
    FbBuilder
  },
  props: {
    /**
     * @property {String} [formHandlerPreviewParam=_ncfb_preview]
     * When form in preview window is submitted (user clicked on "Send" button)
     * ajax request will be made to url defined in [formHandlerUrl](#formhandlerurl).
     * To distinquish real request from form builders "preview" request additional
     * param is added. This will allow you to detect form builders "preview" request
     * and make form validation without eg. sending real email.
     *
     * ```php
     * try {
     *   // This will validate data passed to form.
     *   $form->process();
     *
     *   // Now you can save your $data to eg. database.
     *   $data = $form->getData();
     *
     *   if (!isset($_GET['_ncfb_preview'])) {
     *     // Do something clever, eg. send email to the admininistrator.
     *   }
     * } catch (\Hspg\FormBuilder\Exception\ValidationException $e) {
     *   header('HTTP/1.0 400 Bad Request');
     *   header('Content-Type: application/json');
     *   echo json_encode([
     *     'errors' => $e->getErrors()
     *   ]);
     * }
     * ```
    */
    formHandlerPreviewParam: {
      type: String,
      default: '_ncfb_preview'
    },
    /**
     * @property {String} formHandlerUrl
     * If `formHandlerUrl` is not defined form preview will have disabled
     * "submit" button.
     *
     * Request from form preview will be send with custom parameter defined
     * in [formHandlerPreviewParam](#formhandlerpreviewparam) (by default
     * it's `_ncfbpreview=1`).
     */
    formHandlerUrl: String,
    /**
     * @property {Boolean} [nameOption=true] Enable form naming functionallity.
     */
    nameOption: {
      type: Boolean,
      default: true
    },
    /**
     * @property {Boolean} [descriptionOption=true] Enable form descripting functionallity.
     */
    descriptionOption: {
      type: Boolean,
      default: true
    },
    /**
     * @property {Boolean} [listSetting=true] Enable selecting
     * which control should be displayed on list view.
     */
    listSetting: {
      type: Boolean,
      default: true
    },
    /**
     * @property {Function} onPreviewSubmit Function called after user clicks
     * "Send" button in the form preview modal, but before form make ajax request.
     *
     * ```js
     * hspg.form.builder.create({
     *    el: '#builder',
     *    onPreviewSubmit(form) {
     *        // Do something important here
     *        // eg. save created in builder form.
     *
     *        // Send form.
     *        form.send()
     *    }
     * })
     * ```
     */
    onPreviewSubmit: {
      type: Function,
      default: null
    },
    /**
     * @property {Number} [previewZIndex=1000] Css `z-index` value set on root element of the preview modal.
     */
    previewZIndex: {
      type: Number,
      default: 1000
    },
    settings: {
      type: Object,
      default() {
        return {};
      }
    },
    value: {
      type: Object,
      default: null
    },
    /**
     * @property {Object} Themes list.
     *
     *     {
     *         ncfbdef: { name: 'Default', url: '/url/to/theme.css' }
     *     }
     */
    themes: {
      type: Object,
      required: true
    }
  },
  controls: controls,
  // This property stores UID of the parent of the last dragged element.
  // It is used inside CellTransition to move only elements inside this parent
  // rather then all cells in form.
  transitionInsideParent: null,
  data() {
    return {
      animation: 'cells',
      tips: [],
      // Stores instances of ControlPropsModal for every control type.
      props: [],
      // Control properties modal object.
      ctrlProps: {
        // Control data to show in controls properties modal.
        control: null
      },
      // Stores all existing cells in form builder. Every cell adds itself
      // after mounting, and removing itself when is destroyed.
      cells: {},
      // If true form settings modal is visible.
      formSettingsOpened: false,
      /**
       * @private {Boolean} exportModal Resolve if exporting modal is visible or not.
       */
      exportModal: false,
      /**
       * @private {Boolean} importModal Resolve if importing modal is visible or not.
       */
      importModal: false,
      ctrlSelector: {
        /**
         * @private {Boolean} [ctrlSelector.opened=false] Resolve if "control selection" modal is visible or not.
         */
        opened: false,
        target: null,
        insertMode: 'before'
      },
      form: normalizeFormDef(this.value),
    }
  },
  methods: {
    /**
     *
     * @param {String} target Uid of target element. If target is "undefined" (or false, 0, null)
     * new control will be inserted as last item on root level.
     * @param {String} control Control alias.
     * @param {String} insertMode Where to insert new control (before, after).
     * @param options
     * @return {Object} Control data.
     */
    addCtrl(target, control, insertMode, options = {}) {
      const me = this;

      if (target) {
        target = me.findItem(target)
      }

      let data

      if (typeof control === 'string') {
        data = clone(me.getCtrlDef(control).info.data);
        data.uid = me.form.uidIndex++
        data.type = control
      } else {
        data = control
      }

      // Check if some control wants to handle adding itself.
      let added = false
      me.iterateCtrlDefs((def) => {
        const Ctrl = def.cmp
        if (!added && isFn(Ctrl.onCtrlAdd) && Ctrl.onCtrlAdd.call(me, target, data, insertMode, options) === true) {
          added = true
        }
      })
      if (added) {
        me.$emit('change', {type: 'add'})
        return data
      }

      if (!target) {
        this.form.items.push(data)
        me.$emit('change', {type: 'add'})
      } else {
        switch (insertMode) {
          case 'before':
            target.items.splice(target.index, 0, data)
            break
          case 'after':
            target.items.splice(target.index + 1, 0, data)
            break
          default:
            throw new Error('Can not add new control')
            break;
        }
        me.$emit('change', {type: 'add'})
      }

      me.syncCellsWithDom()

      return data
    },
    addTip(name, message) {
      this.tips.push({name, message})
    },
    /**
     * Clone control and place as next element.
     * This don't clone container.
     *
     * @param uid
     * @returns {Object} Control data.
     */
    cloneCtrl(uid) {
      let me = this,
          {item} = me.findItem(uid);

      // Don't clone container.
      if (item && item.type !== 'cnt.cnt') {
        // Add new control.
        let newCtrl = me.addCtrl(uid, item.type, 'after', {})

        // Change the default props for the cloned element props.
        for (let k in item) { // jshint ignore:line
          // Skip loop if the property is from prototype
          if (!item.hasOwnProperty(k) || k === 'uid') continue // jshint ignore:line

          newCtrl[k] = item[k]
        }

        return newCtrl
      }
    },
    closeProps() {
      const
          me = this,
          props = me.props;

      let len = props.length;
      while (len--) {
        props[len].opened = false
      }
    },
    /**
     * How to use it:
     *
     *     const { item } = $root.findItem(uid)
     *     if (item) {
     *         // founded.
     *     }
     *
     * @param uid
     * @param tree
     * @param parent
     * @returns {*}
     */
    findItem(uid, parent = null, items = null) {
      const me = this;

      items = items ? items : (parent ? parent.items : me.form.items);

      let index = items.length, item, result, idx
      while (index--) {
        item = items[index]

        if (item.uid == uid) {
          return {items, index: index, item, parent}
        }

        if (item.items/* && item.type !== 'radio.radio' && item.type !== 'checkbox.checkbox'*/) {
          // Section is special case.
          if (item.type.indexOf('cnt.') === 0) {
            idx = item.items.length
            while (idx--) {
              result = me.findItem(uid, item, item.items[idx])
              if (result.item) {
                return result
              }
            }
          } /*else {
                        // This is not used coz there is (for now) no control with items.
                        result = me.findItem(uid, item)
                        if (result.item) {
                            return result
                        }
                    }*/
        }
      }

      return {}
    },
    /**
     * Return value from form object.
     * @param {String} key
     * @param {*}
     */
    get(key, defaults = null) {
      return get(key, this.form, defaults)
    },
    getCtrlDefs(visibility = 'public') {
      const
          controls = this.$options.controls,
          items = {};
      let key, def, internal

      for (key in controls) {
        if (key.indexOf('.root') === -1) {
          continue
        }

        def = controls[key]
        internal = def.info.internal

        if (visibility === 'all' || (visibility === 'public' && !internal) || (visibility === 'internal' && internal)) {
          items[key] = controls[key]
        }
      }

      return items
    },
    getCtrlDef(alias) {
      const controls = this.$options.controls

      if (controls.hasOwnProperty(alias)) {
        return controls[alias]
      }

      throw new TypeError(`Control "${alias}" can't be found.`)
    },
    /**
     * Return all subtypes of control.
     *
     * @param {String} alias Alias of the control.
     */
    getCtrlSubtypesDef(alias) {
      const
          controls = this.$options.controls,
          defs = {};
      let key, parts
      for (key in controls) {
        parts = key.split('.')
        if (parts[1] !== 'root' && parts[0] === alias) {
          defs[key] = controls[key]
        }
      }

      return defs
    },
    /**
     * Return form object.
     *
     * @return {Object}
     */
    getValue() {
      return clone(this.form)
    },
    /**
     * Iterate through controls definitions.
     *
     *     $root.iterateControlsDefinitions((item, name) => {
     *         console.log(name);
     *     })
     *
     * @param fn
     */
    iterateCtrlDefs(fn) {
      const controls = this.getCtrlDefs('all');

      for (let key in controls) {
        const ctrl = controls[key]

        if (ctrl.items) {
          for (let k in ctrl.items) {
            fn(ctrl.items[k], k)
          }
        } else {
          fn(ctrl, key)
        }
      }

    },
    moveCtrl(target, control, insertMode) {
      const me = this;

      // Skip moving if location is the same.
      if (target == control) {
        return
      }

      target = me.findItem(target)
      control = me.findItem(control)

      if (target.item && control.item) {
        const
            tItems = target.items,
            cItems = control.items;

        // If we are trying to move container into itself.
        if (target.item.type === 'ghost.ghost' && target.parent.uid === control.item.uid) {
          return
        }

        // Skip moving if its after prev sibling.
        if (tItems === cItems && target.index + 1 === control.index && insertMode === 'after') {
          return
        }

        // Remove control from items.
        me.removeCtrl(control.item.uid)

        // Element which will be moved was removed from "tree"
        // so index of target element could change. Get it one
        // more time.
        target = me.findItem(target.item.uid)

        if (insertMode === 'after') {
          tItems.splice(target.index + 1, 0, control.item)
        } else if (insertMode === 'before') {
          if (target.index === 0) {
            tItems.unshift(control.item)
          } else {
            tItems.splice(target.index, 0, control.item)
          }
        }

        this.$emit('change', {type: 'move'})
      }
    },
    /**
     * Open control selector modal.
     *
     * @param {} target
     * @param {String} insertMode='before' Where put new control (before or after target defined as first argument).
     */
    openControlSelector(target, insertMode = 'before') {
      const selector = this.ctrlSelector
      selector.target = target
      selector.insertMode = insertMode
      selector.opened = true
    },
    /**
     * Show form settings modal.
     * @return void
     */
    openSettings(selectedTab, focusedInput) {
      this.formSettingsOpened = true
      if (selectedTab) {
        this.$nextTick(() => {
          const formSettings = this.$refs.builder.$refs.formSettings
          formSettings.tab = selectedTab
          if (focusedInput) {
            formSettings.$el.querySelector(`[prop=${focusedInput}]`).focus()
          }
        })
      }
    },
    /**
     * Hide form settings modal.
     * @return void
     */
    hideSettings() {
      this.formSettingsOpened = false
    },
    /**
     * Open form preview modal.
     */
    openPreview() {
      const me = this

      if (!me.previewModal) {
        const $el = $('<div id="ncfb-preview-modal"></div>').appendTo('body')

        me.previewModal = new FbPreviewModal({
          el: $el[0],
          propsData: {
            builder: me
          }
        })
      }

      me.previewModal.open()
    },
    /**
     * Open control properties modal.
     *
     * How to us it:
     *
     *     $root.openProps(ctrlUid, { tab: 'basic', focus: 'label'});
     *
     * @param {String} ctrlUid Control uid.
     * @param {Object} config
     */
    openProps(ctrlUid, config = {}) {
      const me = this,
          props = me.ctrlProps

      props.control = me.findItem(ctrlUid).item

      if (Object.keys(config).length !== 0) {
        this.$root.on('ctrlPropsOpen', (prop) => {
          // Set tab.
          if (config['tab']) {
            prop.tab = config['tab']
          }

          // Set focus on ref.
          if (config['focus']) {
            if (prop.$refs[config['focus']] !== undefined) {
              prop.$refs[config['focus']].focus()
            } else {
              throw new TypeError(`Ref "${config['focus']}" cannot be found in ${prop.data.type.split('.')[0]}/Props.vue`)
            }
          }

          // Disable listening to reset all configs.
          this.$root.$off('ctrlPropsOpen')
        })
      }
    },

    removeCtrl(uid) {
      const
          me = this,
          item = me.findItem(uid);

      if (item) {
        // This is need for CellTransition component
        const cells = this.cells
        let key, cell
        for (key in cells) {
          cell = cells[key]
          cell.pos = cell.el.getBoundingClientRect()
          cell.width = cell.el.clientWidth
          cell.height = cell.el.clientHeight
        }

        // Check if some control wants to handle removing itself.
        let removed = false
        me.iterateCtrlDefs((def) => {
          const Ctrl = def.cmp
          if (isFn(Ctrl.onCtrlRemove) && Ctrl.onCtrlRemove.call(me, item) === true) {
            removed = true
          }
        })
        if (removed) {
          this.$emit('change', {type: 'remove'})
          return
        }

        // Remove control from items.
        if (item.index === 0) {
          item.items.shift()
        } else {
          item.items.splice(item.index, 1)
        }
        this.$emit('change', {type: 'remove'})
      } else {
        console.warn('[ncfb] Can not remove item "' + uid + '" - not exists.')
      }
    },
    /**
     * Sets form definition.
     * @return {FormComposer}
     */
    setValue(obj) {
      this.form = normalizeFormDef(obj)
      return this
    },
    /**
     * Updates cells position and sizes with dom. This is needed for
     * animation (CellTransition).
     */
    syncCellsWithDom() {
      const cells = this.cells

      for (let key in cells) { // jshint ignore:line
        const cell = cells[key]
        cell.pos = cell.el.getBoundingClientRect()
        cell.width = cell.el.clientWidth
        cell.height = cell.el.clientHeight
      }
    },
    updateCtrl(uid, key, value) {
      const me = this,
          {item} = me.findItem(uid);

      if (item) {
        // Check if some control wants to handle updating itself.
        let updated = false
        me.iterateCtrlDefs((def) => {
          const Ctrl = def.cmp
          if (isFn(Ctrl.onCtrlUpdate) && Ctrl.onCtrlUpdate.call(me, item, key, value) === true) {
            updated = true
          }
        })
        if (updated) {
          this.$emit('change', {type: 'control-update'})
          return
        }

        const rules = get(key, (me.getCtrlDef(item.type).info.validators || {})[item.type] || {})
        if (rules) {
          const errors = each(rules, (rule, key, loop) => {
            rule = isArr(rule) ? clone(rule) : [rule]
            const name = rule.shift()

            if (!validators[name].apply(null, [value].concat(rule))) {
              loop.stop()
              return tpl.apply(null, [validators[name].msg].concat(rule))
            }
          })

          if (errors.length) {
            return errors
          }
        }

        set(key, value, item)
        this.$emit('change', {type: 'control-update'})

        return []
      }
    },
    /**
     * This method should be used to update any form property.
     * @param key
     * @param value
     */
    updateForm(key, value) {
      if (get(key, this.form) === value) {
        return []
      }

      const rules = get(key, formValidators)

      if (rules) {
        const errors = each(rules, (rule, key, loop) => {
          rule = isArr(rule) ? clone(rule) : [rule]
          const name = rule.shift()

          if (!validators[name].apply(null, [value].concat(rule))) {
            loop.stop()
            return tpl.apply(null, [validators[name].msg].concat(rule))
          }
        })

        if (errors.length) {
          return errors
        }
      }

      set(key, value, this.form, true)
      this.$emit('change', {type: 'form-update'})

      return []
    },
    removeTip(name) {
      const tips = this.tips;

      let len = tips.length, tip;

      while (len--) {
        tip = tips[len]
        if (tip.name === name) {
          tips.splice(len, 1)
          break;
        }
      }
    }
  },
  mounted() {
    this.$el.style.position = 'relative'

    const
        rect = this.$el.getBoundingClientRect(),
        width = rect.width - 60,
        left = rect.x

    this.$tipsEl = $(`<div style="position: fixed; bottom: 0; left: ${left}px; background: #fff; padding: 2vh 1vw; width: ${width}px; box-shadow: 0px -2px 5px -2px rgba(0, 0, 0, .2);
    border-radius: 4px 4px 0 0;
    font-family: Archivo, sans-serif;
    font-size: .8rem; display: none;"></div>`).appendTo('body')

    // Remove hint when click outside.
    $(window).on('click', (e) => {
      let $target = $(e.target)

      if (!$target.closest('.ncfb-ui-hint').length) {
        $('.ncfb-ui-hint-content').remove()
      }
    })
  },
  watch: {
    tips() {
      const tips = this.tips;
      let len = tips.length, messages = [];

      if (len === 0) {
        this.$tipsEl.css('display', 'none')
      } else {
        this.$tipsEl.css('display', 'block')
      }

      while (len--) {
        messages.push(tips[len].message)
      }
      this.$tipsEl.html(`<svg style="width: 20px; vertical-align: middle;" version="1.1" id="Light_bulb" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
	 viewBox="0 0 20 20" enable-background="new 0 0 20 20" xml:space="preserve">
<path fill="#DA2764" d="M7.186,19.172C7.975,19.682,8.887,20.027,10,20c1.111,0.027,2.025-0.318,2.814-0.828L12.797,17H7.203
	L7.186,19.172z M12.697,16c0-4.357,4.63-5.848,4.283-10.188c-0.218-2.738-2.073-5.81-6.98-5.81S3.238,3.074,3.019,5.813
	C2.672,10.152,7.303,11.643,7.303,16H12.697z M5,6c0.207-2.598,2.113-4,5-4c2.886,0,4.654,1.371,4.861,3.969
	c0.113,1.424-0.705,2.373-1.809,3.926C12.238,11.041,11.449,12.238,11,14H9c-0.449-1.762-1.238-2.959-2.053-4.106
	C5.844,8.342,4.886,7.424,5,6z"/>
</svg>
` + messages.join(' | '))
    }
  }
})
</script>



