<template>
    <div class="ncfb-app-body">
        <fb-cell-transition>
            <fb-cell
                    v-for="(item, index) in items"
                    :key="item.uid"
                    :index="index"
                    :control="item"
                    :dropzoneRenderTop="index == 0"
                    :dropzoneRenderBottom="true"></fb-cell>
        </fb-cell-transition>
        <fb-control-select></fb-control-select>
    </div>
</template>

<script>
    // FORM BUILDER BODY
    // ------------------------------------------------------------------------
    // This component is form builder body, here you can create, update, move,
    // and remove controls. Here is a code responsible for dragging and dropping.

    // WARNING: Do not change name of the class "ncfb-app-body"; it's like that
    // for a reason, to be uniqual in whole application (that's why it's not
    // "ncfb-body" for example); it's used below in "interact" instance as a
    // dragging & dropping "restriction" area.
    // @ofca 2018/11/29

    import $ from 'cash-dom'
    import interact from 'interactjs'
    import FbCell from './components/cell/BuilderCtrl'
    import FbCellTransition from './components/CellTransition'
    import FbControlSelect from './components/controls/ControlSelect'

    export default {
        name: 'fb-body',
        components: { FbCell, FbCellTransition, FbControlSelect },
        props: ['items'],
        mounted() {
            const me = this;
            let ghost, update, dragUid;
            interact('.ncfb-dragable', { context: this.$el })
                .styleCursor(false)
                .draggable({
                    restrict: {
                        restriction: '.ncfb-app-body',
                        endOnly: true
                    },
                    onstart(ev) {
                        const target = ev.target,
                            height = target.clientHeight;

                        target.classList.add('is-dragged')

                        dragUid = target.getAttribute('uid')

                        const
                            { items } = me.$root.findItem(dragUid),
                            { info } = me.$root.getCtrlDef(items[0].type)

                        // Create ghost element.
                        ghost = $('<div>'+info.name+'</div>').css({
                            top: (ev.pageY - (height / 2)) + 'px',
                            left: ev.pageX + 'px',
                            opacity: 0
                        })
                            .addClass('ncfb-ghost')
                            .appendTo('body')

                        setTimeout(() => {
                            if (ghost) {
                                update = true;
                                ghost.css({
                                    // width: '20px',
                                    // height: '20px',
                                    opacity: 1
                                });
                            }
                        }, 10);
                    },
                    onmove(ev) {
                        if (update) {
                            ghost.css({
                                top: ev.pageY + 2,
                                left: ev.pageX + 2
                            })
                        }
                    },
                    onend(ev) {
                        const target = $(ev.target);

                        ghost.remove()
                        ghost = null
                        update = false
                    }
                })

            interact('.ncfb-dropzone', { context: this.$el })
                .styleCursor(false)
                .dropzone({
                    overlap: 'pointer',
                    ondragenter(ev) {
                        ghost.addClass('ncfb-drop-ready')
                        ev.target.classList.add('ncfb-drop-ready')
                    },
                    ondragleave(ev) {
                        ghost.removeClass('ncfb-drop-ready')
                        ev.target.classList.remove('ncfb-drop-ready')
                    },
                    ondrop(ev) {
                        const
                            target = ev.target,
                            // Control uid.
                            uid = target.getAttribute('uid'),
                            where = target.getAttribute('where'),
                            $root = me.$root,
                            { item } = $root.findItem(uid);

                        target.classList.remove('ncfb-drop-ready')

                        if (!item) {
                            // This should never happend.
                            return
                        }

                        $root.syncCellsWithDom()

                        const draggedItemParent = $root.findItem(dragUid).parent
                        if (draggedItemParent !== null) {
                            // Mark
                            $root.$options.transitionInsideParent = draggedItemParent.uid
                        }

                        // Trying to drop control into control.
                        if (!where) {
                            throw new Error('Not implemented')
                            const
                                // Insert "two cell" control after target control.
                                two = $root.addCtrl(uid, 'twoCell', 'after'),
                                // Get dragging item.
                                {item: dragItem, parent} = $root.findItem(dragUid);

                            // Remove target item from items.
                            $root.removeCtrl(item.uid)
                            // Remove dragged item from items.
                            $root.removeCtrl(dragItem.uid)
                            // Insert target as first elemenet
                            two.items.push(item)
                            // And dragged as second
                            two.items.push(dragItem)

                            // If control was dragged from "two cell" control we need
                            // to remove it.
                            if (parent.type === 'twoCell') {
                                // Place one control that left inside "two cell" after it
                                $root.addCtrl(parent.uid, parent.items[0], 'after')
                                // Remove "two cell" control now.
                                $root.removeCtrl(parent.uid)
                            }
                        } else {
                            $root.moveCtrl(uid, dragUid, where)
                        }

                        setTimeout(() => {
                            me.$root.$options.transitionInsideParent = null
                        }, 1000)
                    }
                })
        }
    }
</script>

<style>
    .ncfb-ghost {
        transition: opacity .2s ease-out;
        z-index: 9000;
        position: absolute;
        border-radius: 4px;
        background: #6e6b70;
        padding: .4vw .4vw;
        font-size: .8rem;
        color: #fff;
        font-family: Archivo, sans-serif;
    }
    .ncfb-ghost.ncfb-drop-ready {
        background: #15a4fa;
    }
</style>
<style scoped>
    .ncfb-app-body {
        padding: 25px 20px 2vh 20px;
        margin-top: 1rem;
        width: 100%;
        background: #FAFBFC;
        border-radius: 10px;
        box-shadow: 0 0 7px 0px rgba(0, 0, 0, .1);
        overflow: hidden;
    }

    .is-dragged {
        opacity: .5;
        border-style: dashed;
    }
    .cells-move {
        transition: all 1s;
    }
</style>



