<template>
    <div class="ncfb-select">
        <span class="ncfb-value" :class="{ 'ncfb-value-focused': opened_ }" @click="opened_ = true"><span v-html="renderValue"></span> <ui-icon class="ncfb-arrow" name="arrow-down" color="#000"></ui-icon></span>
        <transition name="fade">
            <div v-show="opened" class="ncfb-items" ref="items" :style="{ width: itemsWidth }">
                <div v-for="item in items" @click="itemClicked(item)">
                    <slot name="items" :item="item"></slot>
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
    /**
        # Usage:

            <ui-select
                items-width="400px"
                :items="items"
                id-key="id"
                name-key="name"
                :value="value"
                @change="$root.updateCtrl(data.uid, 'valueType', $event)">

                <template slot="items" slot-scope="{ item }">
                    {{ item.name }}
                </template>
            </ui-select>
     */
    import UiIcon from './Icon'
    import $ from 'cash-dom'
    import { isObj, isNum, isStr } from './../../util/util'

    export default {
        name: 'ui-select',
        components: { UiIcon },
        props: {
            items: {
                type: Array,
                default: () => []
            },
            itemsWidth: {
                type: String,
                default: '200px'
            },
            idKey: {
                type: String,
                required: true
            },
            nameKey: {
                type: String,
                required: true
            },
            value: {
                type: [Object, String, Number],
                default: () => { return { id: null, name: null } }
            }
        },
        data() {
            return {
                opened_: false,
                value_: this.normalizeValue(this.value)
            }
        },
        mounted() {
            $('html').on('click.ui-select', (e) => {
                if ($(e.target).closest('.ncfb-select').length === 0) {
                    this.opened_ = false
                }
            })
        },
        beforeDestroy() {
            $('html').off('click.ui-select')
        },
        methods: {
            itemClicked(item) {
                this.value_ = item
                this.$emit('change', { target: this.$el, value: item, cmp: this })
                this.opened_ = false
            },
            normalizeValue(value) {
                if (isNum(value) || isStr(value)) {
                    value = { id: value }
                }
                if (isObj(value)) {
                    if (!value.id) {
                        value.id = null
                    }
                    if (!value.name) {
                        value.name = null
                    }
                }

                return value
            }
        },
        computed: {
            opened() {
                const me = this
                if (me.opened_) {
                    const
                        $el = me.$el,
                        $items = me.$refs.items,
                        bodyRect = document.body.getBoundingClientRect(),
                        elemRect = $el.getBoundingClientRect();

                    $items.style.position = elemRect.top - bodyRect.top + 'px'
                }

                return me.opened_
            },
            renderValue() {
                const
                    me = this,
                    value = me.value_,
                    nameKey = me.nameKey;

                if (value[nameKey]) {
                    return value[nameKey]
                }

                const
                    items = me.items,
                    idKey = me.idKey,
                    id = value[idKey];
                let len = items.length
                while (len--) {
                    if (items[len][idKey] == id) {
                        return items[len][nameKey]
                    }
                }

                return ''
            }
        },
        watch: {
            value(newVal, oldVal) {
                this.value_ = this.normalizeValue(newVal)
            }
        }
    }
</script>
<style scoped>
    .ncfb-value {
        padding: 1% 2%;
        transition: border-color 500ms ease-out;
        outline: none;
        border: 1px solid #e6e6e6;
        background: #fff;
        width: 100%;
        display: inline-block;
        border-radius: 12px;
        position: relative;
        cursor: pointer;
        font-size: .9rem;
    }
    .ncfb-value:hover {
        border-color: #ace5f9;
    }
    .ncfb-value-focused {
        border-color: #03A9F4 !important;
    }
    .ncfb-arrow {
        position: absolute;
        right: 1%;
        top: 17%;
    }
    .ncfb-items {
        position: absolute;
        z-index: 3000;
        max-height: 300px;
        overflow-x: hidden;
        overflow-y: auto;
        background: #fff;
        box-shadow: 0 0px 40px -1px rgba(0, 0, 0, .1);
    }
    .fade-enter-active {
        animation-duration: 260ms;
        animation-fill-mode: both;
        animation-name: fadeIn;
    }
    .fade-leave-to {
        animation-duration: 260ms;
        animation-fill-mode: both;
        animation-name: fadeOut;
    }
    @keyframes fadeIn {
        0%{
            opacity: 0;
            /*transform: scale(0.3) translate3d(0,0,0);*/
            transform: translateY(-20%);
            transform-origin: top;
        }
        100%{
            opacity: 1;
            /*transform: scale(1) translate3d(0,0,0);*/
            transform: translateY(0)
        }
    }
    @keyframes fadeOut {
        0%{
            opacity: 1;
            /*transform: scale(1) translate3d(0,0,0);*/
            transform-origin: bottom;
            transform: translateY(0)
        }
        100%{
            opacity: 0;
            /*transform: scale(0.3) translate3d(0,0,0);*/
            transform: translateY(20%);
        }
    }
</style>



