<template>
    <span class="ncfb-icon" :ncfbhint="hint" :ncfbhint-position="hintPosition" v-html="icon__"></span>
</template>

<script>
    export default {
        name: 'ui-icon',
        props: {
            name: {
                type: String,
                required: true
            },
            width: {
                type: String,
                default: '20'
            },
            color: {
                type: String,
                color: '#000'
            },
            hint: {
                type: String
            },
            hintPosition: {
                type: String,
                default: 'top'
            }
        },
        data() {
            return {}
        },
        computed: {
            icon__() {
                switch (this.name) {
                    case 'edit':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M17.6 2.4C16 1 15 1.2 15 1.2l-6 6L2.2 14l-1 4.8 4.8-1 6.7-6.8 6-6s.3-1.1-1.1-2.6zm-12 14.8l-1.5.4-.7-1-1-.7.4-1.6.5-.4s.8 0 1.8 1 1 1.8 1 1.8l-.4.5z"/></svg>'
                    case 'help':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M10 .4a9.6 9.6 0 1 0 0 19.2A9.6 9.6 0 0 0 10 .4zm-.2 15.2c-.8 0-1.3-.6-1.3-1.4 0-.7.6-1.3 1.3-1.3.9 0 1.4.6 1.4 1.4 0 .8-.6 1.3-1.4 1.3zm3.3-6.5l-1 1-.6.3-.6.7v.6H8.6c0-.8 0-1.3.3-1.6.5-.6 1.6-1.3 1.6-1.3l.4-.4.3-.8c0-.3 0-.6-.2-1-.2-.2-.6-.3-1.1-.3-.5 0-.9.1-1 .5a2 2 0 0 0-.4 1V8H6.4c0-1.4.5-2.4 1.4-3 .6-.3 1.3-.5 2.1-.5 1 0 2 .3 2.7.8.7.5 1 1.3 1 2.3 0 .6-.1 1-.5 1.6z"/></svg>'
                    case 'close':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M10 1.6a8.4 8.4 0 1 0 0 16.8 8.4 8.4 0 0 0 0-16.8zm4.8 11.5L13 14.8l-3.1-3-3 3L5.1 13l3-3.1-3-3L7 5.1l3.1 3 3-3L14.9 7l-3 3.1 3 3z"/></svg>'
                    case 'arrow-down':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M13.4 7.9c.3-.3.7-.3 1 0 .3.2.3.7 0 1l-4 3.8c-.2.2-.6.2-.9 0L5.6 8.8a.7.7 0 0 1 0-1c.3-.2.7-.2 1 0L10 11l3.4-3.1z"/></svg>'
                    case 'settings':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M16.8 10c0-1 .6-1.9 1.6-2.4a9 9 0 0 0-.7-1.7c-1 .3-2-.2-2.7-.9-.7-.7-1-1.6-.7-2.7l-1.7-.7c-.5 1-1.6 1.6-2.6 1.6s-2-.6-2.6-1.6l-1.7.7c.3 1 0 2-.7 2.7-.7.7-1.6 1.2-2.7.9l-.7 1.7C2.6 8 3.2 9 3.2 10s-.6 2-1.6 2.6l.7 1.7c1-.3 2 0 2.7.7.7.7 1 1.6.7 2.7l1.7.7c.5-1 1.6-1.6 2.6-1.6s2 .6 2.6 1.6a9 9 0 0 0 1.7-.7c-.3-1 0-2 .7-2.7.7-.7 1.6-1.2 2.7-.9l.7-1.7c-1-.5-1.6-1.4-1.6-2.4zM10 13.7a3.7 3.7 0 1 1 0-7.4 3.7 3.7 0 0 1 0 7.4z"/></svg>'
                    case 'desktop':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M18 1H2a2 2 0 0 0-2 2v11c0 1.1.9 2.2 2 2.4l4.3.9S2.6 19 5 19h10c2.4 0-1.3-1.7-1.3-1.7l4.3-1c1.1-.1 2-1.2 2-2.3V3a2 2 0 0 0-2-2zm0 13H2V3h16v11z"/></svg>'
                    case 'plus':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M10 1.6a8.4 8.4 0 1 0 0 16.8 8.4 8.4 0 0 0 0-16.8zm5 9.4h-4v4H9v-4H5V9h4V5h2v4h4v2z"/></svg>'
                    case 'copy':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M11 0H3a1 1 0 0 0-1 1v12a1 1 0 0 0 1 1h5v2h2v-2H8.001v-2H10v-2H8v2H4V2h6v4h2V1a1 1 0 0 0-1-1zM8 7v1h2V6H9a1 1 0 0 0-1 1zm4 13h2v-2h-2v2zm0-12h2V6h-2v2zM8 19a1 1 0 0 0 1 1h1v-2H8v1zm9-13h-1v2h2V7a1 1 0 0 0-1-1zm-1 14h1a1 1 0 0 0 1-1v-1h-2v2zm0-8h2v-2h-2v2zm0 4h2v-2h-2v2z"/></svg>'
                    case 'back':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M10 .4a9.6 9.6 0 1 0 0 19.2A9.6 9.6 0 0 0 10 .4zm0 17.2a7.6 7.6 0 1 1 0-15.2 7.6 7.6 0 0 1 0 15.2zm0-12.1L5.5 10l4.5 4.5V12h4V8h-4V5.5z"/></svg>'
                    case 'upload':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M9.967 8.193L5 13h3v6h4v-6h3L9.967 8.193zM18 1H2C.9 1 0 1.9 0 3v12c0 1.1.9 2 2 2h4v-2H2V6h16v9h-4v2h4c1.1 0 2-.9 2-2V3c0-1.1-.9-2-2-2zM2.5 4.25a.75.75 0 110-1.5.75.75 0 010 1.5zm2 0a.75.75 0 110-1.5.75.75 0 010 1.5zM18 4H6V3h12.019L18 4z"/></svg>'
                    case 'download':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path fill="'+this.color+'" d="M19.059 10.898l-3.171-7.927A1.543 1.543 0 0014.454 2H12.02l.38 4.065h2.7L10 10.293 4.9 6.065h2.7L7.98 2H5.546c-.632 0-1.2.384-1.434.971L.941 10.898a4.25 4.25 0 00-.246 2.272l.59 3.539A1.544 1.544 0 002.808 18h14.383c.755 0 1.399-.546 1.523-1.291l.59-3.539a4.22 4.22 0 00-.245-2.272zm-2.1 4.347a.902.902 0 01-.891.755H3.932a.902.902 0 01-.891-.755l-.365-2.193A.902.902 0 013.567 12h12.867c.558 0 .983.501.891 1.052l-.366 2.193z"/></svg>'
                    case 'loader':
                        return '<svg class="loader" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" width="40px" height="40px" viewBox="0 0 50 50" style="enable-background:new 0 0 50 50;" xml:space="preserve"><path fill="#999" d="M25.251,6.461c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615V6.461z"></path></svg>'
                    case 'reset':
                        return '<svg style="width: '+this.width+'px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 40.5 42.17"><path fill="'+this.color+'" d="M29.4,2.65A20.85,20.85,0,0,0,8,3.64L4.92.59A2,2,0,0,0,1.5,2V13.18a2,2,0,0,0,2,2H14.67a2,2,0,0,0,1.42-3.42L13.81,9.48A13,13,0,1,1,7.68,26.56a2,2,0,0,0-2.43-1L1.43,26.66A2,2,0,0,0,.15,29.32,21,21,0,1,0,29.4,2.65Z"/></svg>'
                }
            }
        }
    }
</script>

<style>
    .ncfb-app .ncfb-icon {
        display: inline-block;
        vertical-align: middle;
    }

    @keyframes ncfbIconLoader {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(359deg);
        }
    }
    .ncfb-app .ncfb-icon .loader path {
        transform-origin: center;
        animation: ncfbIconLoader 1000ms infinite linear forwards;
    }
</style>



