<template>
    <div class="ncfb-body">
        <label @click="$root.openProps(data.uid, { tab: 'basic', focus: 'label' })">{{ data.label }}</label>
        <span v-if="data.help" class="ncfb-help">{{ data.help }}</span>
        <select class="ncfb-select">
            <option v-for="item in data.values" :key="item.uid" :value="item.value" :selected="item.uid == data.selected">{{ item.value }}</option>
        </select>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import { extend, clone } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-select',
    })

    Ctrl.ALIAS = 'select'

    const data = {
        label: 'Label',
        help: '',
        required: false,
        selected: 1,
        itemsUidIndex: 1,
        values: [],
        // Display this field on results list.
        onList: false
    }

    const validators = {}

    const info = {
        internal: false,
        desc: '',
        icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 26 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h24.8v14H.5z"/><path fill="none" stroke="#6e6e6e" d="M12.9 5.3l4.3 4.4M21.6 5.3l-4.4 4.4"/></svg>'
    }

    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Select',
            group: 'inputs'
        }),
        select: extend(clone(info), {
            name: 'Select',
            title: 'Select',
            data: extend(clone(data)),
            validators: validators
        })
    }

    export default Ctrl
</script>
<style scoped>
    .ncfb-label {
        width: 80%;
    }
    .ncfb-body label {
        display: inline-block;
        margin: 10px 0;
    }
    .ncfb-help {
        font-size: .8rem;
        display: inline-block;
        font-style: italic;
        color: gray;
    }
    .ncfb-select {
        width: 100%;
        height: 40px;
        resize: vertical;
        padding: .5vw;
        border: 1px solid #c3c3c3;
        transition: all 500ms ease-out;
        outline: none;
        margin-bottom: 10px;
    }
    .ncfb-select:focus {
        border-color: #03A9F4;
    }
</style>



