<template>
    <div class="ncfb-cell">
        <component
                :is="$root.getCtrlDef(control.type).cmp"
                :data="control"
                ref="control"
        ></component>
    </div>
</template>
<script>
    export default {
        name: 'fb-cell',
        props: {
            control: {
                type: Object,
                required: true
            }
        },
        methods: {
            value() {
                return this.$refs.control.value()
            }
        }
    }
</script>



