<template>
    <div class="ncfb-checkboxes">
        <label v-if="data.label != false" class="ncfb-opt-label" @click="$root.openProps(data.uid, { tab: 'basic', focus: 'label' })">{{ data.label }}</label>
        <span v-if="data.help" class="ncfb-help">{{ data.help }}</span>
        <div :class="[layout__]">
            <div v-for="(group, groupIndex) in items__" :class="'ncfb-col ncfb-col-'+(groupIndex+1)">
                <div class="ncfb-checkbox" v-for="item in group">
                    <input type="checkbox" :id="'ctrl-cbx-'+data.uid+'-'+item.uid" style="display:none" class="ncfb-input" :checked="item.checked">
                    <label :for="'ctrl-cbx-'+data.uid+'-'+item.uid" class="ncfb-label"><span></span></label>
                    <label :for="'ctrl-cbx-'+data.uid+'-'+item.uid" class="ncfb-lbl">{{ item.value }}</label>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import { extend, clone } from './../../../util/util'
    let ids = 0;

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fbctrl-checkboxes',
        computed: {
            items__() {
                const
                    me = this,
                    data = me.data,
                    layout = data.layout,
                    colNum = (layout === '1-column' || layout === 'side-by-side') ? 1
                        : (layout === '2-column' ? 2
                            : (layout === '3-column' ? 3 : 1)),
                    arr = [],
                    items = data.items,
                    size = Math.ceil(items.length / colNum);
                let i = 0
                for (; i < items.length; i += size) {
                    arr.push(items.slice(i, i+size))
                }
                return arr
            },
            layout__() {
                return 'ncfb-layout-' + this.data.layout
            }
        }
    })

    Ctrl.ALIAS = 'checkbox'
    const data = {
        help: '',
        required: false,
        label: 'Some optional label',
        // Available values: 1-column, 2-column, 3-column, side-by-side
        layout: '1-column',
        items: [
            { uid: 1, value: 'Some checkbox to check', checked: false }
        ],
        itemsUidIndex: 2,
        // Display this field on results list.
        onList: false
    }

    const validators = {}

    const info = {
        internal: false,
        desc: '',
        icon: '<svg style="width: 20px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="1.5" clip-rule="evenodd" viewBox="0 0 15 16"><path fill="#fff" stroke="#6e6e6e" d="M.5.5h14v14H.5z"/><path fill="none" stroke="#6e6e6e" d="M10.9 4.2L4 11M4.1 4l6.8 6.8"/></svg>'
    }

    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Checkbox',
            group: 'inputs',
            commonDesc: `
                <p>Checkbox are square boxes that are checked (ticked) when activated, like you might see in an official government paper form. They allow you to select single values for submission in a form (or not).</p>
            `,
            desc: `<p>Checkbox are square boxes that are checked (ticked) when activated, like you might see in an official government paper form. They allow you to select single values for submission in a form (or not).</p>`
        }),
        checkbox: extend(clone(info), {
            name: 'Checkbox',
            data: clone(data),
            validators: validators,
            desc: `<p>You can configure checkbox field to have one or more items to "check".</p>`
        })
    }

    export default Ctrl
</script>

<style scoped>
    .ncfb-checkbox {
        display: block;
        vertical-align: middle;
        margin-bottom: 16px;
    }
    .ncfb-opt-label {
        display: inline-block;
        margin: 10px 0;
    }
    .ncfb-help {
        font-size: 12px;
        display: inline-block;
        font-style: italic;
        color: gray;
        margin-bottom: 8px;
    }
    .ncfb-label {
        position: relative;
        width: 40px;
        height: 20px;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
        transform: translate3d(0,0,0);
        display: inline-block;
        vertical-align: top;
    }
    .ncfb-label:before {
        content: "";
        position: relative;
        top: 3px;
        left: 3px;
        width: 34px;
        height: 14px;
        display: block;
        background: #9a9999;
        border-radius: 8px;
        transition: background .2s ease;
    }
    .ncfb-label span {
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        display: block;
        background: white;
        border-radius: 10px;
        box-shadow: 0 3px 8px rgba(60, 60, 60, .5);
        transition: all .2s ease;
    }
    .ncfb-label span:before {
        content: "";
        position: absolute;
        display: block;
        margin: -18px;
        width: 56px;
        height: 56px;
        background: rgba(31, 18, 86, .5);
        border-radius: 50%;
        transform: scale(0);
        opacity: 1;
        pointer-events: none;
    }
    .ncfb-input:checked + .ncfb-label:before {
        background: #88A7F1;
    }
    .ncfb-input:checked + .ncfb-label span {
        background: #526aa4;
        transform: translateX(20px);
        transition: all .2s cubic-bezier(.8,.4,.3,1.25), background .15s ease;
        box-shadow: 0 3px 8px rgba(31, 18, 86, .2);
    }
    .ncfb-input:checked + .ncfb-label span:before {
        transform: scale(1);
        opacity: 0;
        transition: all .4s ease;
    }
    .ncfb-lbl {
        font-size: 13px;
        display: inline-block;
        vertical-align: middle;
        width: calc(100% - 50px);
        padding: 2px 5px 0 5px;
    }

    .ncfb-layout-2-column {
        font-size: 0;
    }
    .ncfb-layout-2-column .ncfb-col {
        display: inline-block;
        width: 50%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-layout-3-column .ncfb-col {
        display: inline-block;
        width: 33%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-layout-side-by-side .ncfb-checkbox {
        display: inline-block;
        margin-right: 20px;
    }
</style>



