<template>
    <span class="ncfb-ui-hint" @mouseover="show" @mouseleave="hide">
        <slot></slot>
    </span>
</template>

<script>
    import $ from 'cash-dom'

    export default {
        name: 'ui-hint',
        props: {
            hint: {
                type: String
            }
        },
        methods: {
            show() {
                // Calc hint position by parent.
                let cords = this.getCoords(this.$el),
                    top = cords.top - 5,
                    left = cords.left + 25,
                    theme = this.$root.theme_;

                // Add hint to dom.
                $('<span class="ncfb-ui-hint-content ncfb '+theme+'"></span>')
                    .appendTo(document.body)
                    .text(this.hint)
                    .css({ top: top, left: left })
            },
            hide() {
                $('.ncfb-ui-hint-content').remove()
            },
            getCoords(elem) {
                let box = elem.getBoundingClientRect(),
                    body = document.body,
                    docEl = document.documentElement,
                    scrollTop = window.pageYOffset || docEl.scrollTop || body.scrollTop,
                    scrollLeft = window.pageXOffset || docEl.scrollLeft || body.scrollLeft,
                    clientTop = docEl.clientTop || body.clientTop || 0,clientLeft = docEl.clientLeft || body.clientLeft || 0,
                    top  = box.top +  scrollTop - clientTop,
                    left = box.left + scrollLeft - clientLeft;

                return { top: Math.round(top), left: Math.round(left) }
            }
        }
    }
</script>

<style>
    .ncfb-ui-hint {
        display: inline-block;
        vertical-align: middle;
        cursor: pointer;
    }
    .ncfb-ui-hint-content {
        position: absolute;
        display: block;
        z-index: 100001;
        padding: 5px 10px;
        color: #fff;
        background: #292929;
        max-width: 300px;
        transition: all 300ms;
        text-align: left;
    }
    .ncfb-ui-hint-content::before {
        content: "";
        position: absolute;
        top: 13px;
        left: -8px;
        border-width: 4px 6px 0 6px;
        border-style: solid;
        border-color: #292929 transparent transparent transparent;
        z-index: 100001;
        transform: translateY(-50%) rotate(90deg)
    }
</style>



