<script>
    import FbLabel from './Label'

    export default {
        props: {
            data: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                error: false
            }
        },
        methods: {
            value() {
                const data = this.data
                const obj = {}
                obj[data.uid] = this.getValue()
                return obj
            },
            getValue() {
                return null
            }
        },
        beforeCreate() {
            this.cmpLbl = FbLabel
        },
        created() {
            this.$root.controls[this.data.uid] = this
        }
    }
</script>


