<template>
    <div class="ncfb-of-cnt">
        <div class="ncfb-of-n">
            <a :class="{ 'ncfb-of-n-a': tab == 'basic' }" @click="tab = 'basic'">Basic data</a>
            <a :class="{ 'ncfb-of-n-a': tab == 'validation' }" @click="tab = 'validation'">Validation</a>
        </div>
        <div class="ncfb-of-f">
            <div v-show="tab == 'basic'">
                <h2 class="ncfb-of-h">Basic data</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Label</label>
                    <input type="text" class="ncfb-of-i" :value="data.label" prop="label" @input="vchange($event)" @blur="setDefault($event)" ref="label">
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Help text</label>
                    <input type="text" class="ncfb-of-i" :value="data.help" prop="help" @input="vchange($event)" @blur="setDefault($event)">
                </div>
                <div class="ncfb-of-g" v-if="$root.listSetting">
                    <label class="ncfb-of-l">Display on list</label>
                    <ui-checkbox :checked="data.onList" @change="vchange($event)" prop="onList"></ui-checkbox>
                </div>
            </div>
            <div v-show="tab == 'validation'">
                <h2 class="ncfb-of-h">Validation</h2>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l">Required</label>
                    <ui-checkbox :checked="data.required" @change="vchange($event)" prop="required"></ui-checkbox>
                </div>
                <div class="ncfb-of-g">
                    <label class="ncfb-of-l" style="vertical-align: top;">Type of file</label>
                    <div class="ncfb-of-i">
                        <p style="margin-bottom: 20px;">Select files that can be uploaded (if no types are checked/specified below, any type of file will be allowed):</p>
                        <div class="ncfb-file-type" v-for="type in $options.fileTypes" :key="type.id">
                            <ui-checkbox :id="type.id" :checked="data.fileTypes.indexOf(type.id) !== -1" @change="onFileTypeChanged(type.id, $event.value)"></ui-checkbox> {{ type.name }}
                            <span class="ncfb-file-desc">{{ type.desc }}</span>
                        </div>
                    </div>
                </div>
                <div class="ncfb-of-g">
                    <div class="ncfb-of-ilm">
                        <h2 class="ncfb-of-h2">Additional allowed extensions</h2>
                        <p style="margin-bottom: 20px;">You can specify extensions of files that will be accepted by this control.</p>
                        File extension: <input type="text" class="ncfb-of-i" v-model="fileExt_" style="width: 20%;" placeholder="eg. jpg">
                        <ui-btn type="add-sm" text="Add" @click="addExt"></ui-btn>
                        <div v-show="fileExtError_">
                            <p style="margin: 10px 0;">We can't find specified extension in our database. You may specify MIME type of the file manually:</p>
                            MIME type: <input type="text" class="ncfb-of-i" v-model="fileMime_" style="width: 20%;">
                            <ui-btn type="add-sm" text="Add" @click="addMime"></ui-btn>
                        </div>
                        <div v-show="data.fileExts.length > 0">
                            <h2 class="ncfb-of-h2">Extensions:</h2>
                            <ul class="ncfb-list">
                                <li v-for="(ext, key) in data.fileExts">{{ ext }} <ui-btn type="list-rm" @click="data.fileExts.splice(key, 1)"></ui-btn></li>
                            </ul>
                        </div>
                        <div v-show="data.fileMimes.length > 0">
                            <h2 class="ncfb-of-h2">Custom MIME types:</h2>
                            <ul class="ncfb-list">
                                <li v-for="(mime, key) in data.fileMimes">{{ mime }} <ui-btn type="list-rm" @click="data.fileMimes.splice(key, 1)"></ui-btn></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
