    export default {
        extends: AbstractProps,
        name: 'fb-file-props',
        components: { FbIcon, UiCheckbox, UiSelect, UiBtn },
        fileTypes: [
            {
                id: 'image',
                name: 'Images',
                desc: 'Image files: JPG, PNG, GIF, BMP, WEBM'
            },
            {
                id: 'docs',
                name: 'Documents',
                desc: 'Document files: DOC/DOCX, ODT, PDF, RTF, TXT'
            },
            {
                id: 'video',
                name: 'Video',
                desc: 'Video files: 3GP, MP4, MPEG, OGV, WEBM, FLV, MKV, VOB, AVI, MOVIE, WMV'
            },
            {
                id: 'audio',
                name: 'Audio',
                desc: 'Audio files: OGA, AAC, FLAC, WMA, WAV, MP3'
            }
        ],
        data() {
            return {
                fileExt_: '',
                fileMime_: '',
                fileExtError_: false
            }
        },
        methods: {
            addExt() {
                const me = this,
                    fileExts = me.data.fileExts,
                    ext = me.fileExt_.replace(/\s+/, '');

                me.fileExtError_ = false

                let mime = null

                each(mimeTypes, (types, firstPart, loop1) => {
                    each(types, (extensions, secondPart, loop2) => {
                        if (extensions.split('|').indexOf(ext) !== -1) {
                            loop1.stop()
                            loop2.stop()
                            mime = firstPart + '/' + secondPart
                        }
                    })
                })

                if (!mime) {
                    me.fileExtError_ = true
                } else {
                    if (fileExts.indexOf(ext) === -1) {
                        fileExts.push(ext)
                    }

                    me.fileExt_ = ''
                    this.$root.$emit('change', { type: 'control-update' })
                }
            },
            addMime() {
                const mime = this.fileMime_.replace(/\s+/, '')

                if (this.data.fileMimes.indexOf(mime) === -1) {
                    this.data.fileMimes.push(mime)
                }

                this.fileMime_ = ''
                this.fileExtError_ = false
                this.$root.$emit('change', { type: 'control-update' })
            },
            addItem() {
                this.data.fileExt.push({
                    uid: this.data.fileExtUid++,
                    name: ''
                })
                this.$root.$emit('change', { type: 'control-update' })
            },
            onFileTypeChanged(type, checked) {
                const types = this.data.fileTypes

                if (checked) {
                    types.push(type)
                    this.data.fileTypes = types.filter((value, index, self) => {
                        return self.indexOf(value) === index
                    })
                } else {
                    const newTypes = []
                    each(types, (value, key, loop) => {
                        if (type !== value) {
                            newTypes.push(value)
                        }
                    })
                    this.data.fileTypes = newTypes
                }

                this.$root.$emit('change', { type: 'control-update' })
            },
            removeItem(uid) {
                const ext = this.data.fileExt
                each(ext, (item, key, loop) => {
                    if (item.uid == uid) {
                        loop.stop()
                        ext.splice(key, 1)
                    }
                })
                this.$root.$emit('change', { type: 'control-update' })
            },
        }
    }
</script>

<style scoped>
    @import './../../../css/options-form.css';

    .ncfb-list {
        margin-left: 40px;
    }

    .ncfb-file-type {
        margin: 10px 0;
    }
    .ncfb-file-desc {
        display: block;
        margin-left: 44px;
        font-size: 13px;
        color: gray;
    }

    .animated-list-enter {
        opacity: 0;
        transform: translateX(30px);
    }

    .animated-list-leave-active {
        position: absolute;
    }

    .ncfb-select-item {
        display: block;
        border-bottom: 1px solid #ececec;
        transition: background-color 100ms ease-out;
        cursor: pointer;
        padding-bottom: 13px;
        background: #fafbfc;
        box-shadow: 0 0 33px 0px rgba(0, 0, 0, .1);
    }
    .ncfb-select-item:hover {
        background-color: #e7f2f5;
    }
    .ncfb-select-item:hover em {
    }
    .ncfb-select-item-name {
        padding: 10px 20px;
        font-weight: bold;
        display: block;
        background: #fff;
        margin-bottom: 7px;
    }
    .ncfb-select-item-desc {
        color: gray;
        padding: 10px 20px 6px 20px;
        display: block;
    }
    .ncfb-ok {
        display: block;
        color: gray;
        padding: 6px 20px 4px 20px;
    }
    .ncfb-ok strong {
        font-weight: normal;
        color: #10c506;
    }
    .ncfb-not {
        display: block;
        color: gray;
        padding: 4px 20px 6px 20px;
    }
    .ncfb-not strong {
        font-weight: normal;
        color: #da2764;
    }
</style>



