<template>
    <div class="ncfb-checkboxes ncfb-ctrl" :class="{ 'ncfb-ctrl-error': error }">
        <component :is="cmpLbl" :label="data.label" :help="data.help"></component>
        <div class="ncfb-input-cnt" :class="[layout__]">
            <div v-for="(group, groupIndex) in items__" :class="'ncfb-col ncfb-col-'+(groupIndex+1)">
                <div class="ncfb-checkbox" v-for="item in group">
                    <input
                        type="checkbox"
                        :id="'ncfb-cbx-'+data.uid+'-'+item.uid"
                        style="display:none"
                        class="ncfb-input"
                        :checked="item.checked"
                        :value="item.value"
                        ref="checkbox">
                    <label :for="'ncfb-cbx-'+data.uid+'-'+item.uid" class="ncfb-label"><span></span></label>
                    <label :for="'ncfb-cbx-'+data.uid+'-'+item.uid" class="ncfb-checkbox-lbl">{{ item.value }}</label>
                </div>
            </div>
        </div>
        <span class="ncfb-error" v-show="error">{{ error }}</span>
    </div>
</template>

<script>
    import Vue from 'vue'
    import UiHint from './../../ui/Hint'
    import UiIcon from './../../ui/Icon'
    import AbstractControl from './../../renderer/AbstractControl'
    import { each } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        components: { UiHint, UiIcon },
        name: 'fb-checkboxes',
        methods: {
            getValue() {
                const vals = []
                each(this.$refs.checkbox, (checkbox) => {
                    if (checkbox.checked) {
                        vals.push(checkbox.value)
                    }
                })

                return vals
            }
        },
        computed: {
            items__() {
                const
                    me = this,
                    data = me.data,
                    layout = data.layout,
                    colNum = (layout === '1-column' || layout === 'side-by-side') ? 1
                        : (layout === '2-column' ? 2
                            : (layout === '3-column' ? 3 : 1)),
                    arr = [],
                    items = data.items,
                    size = Math.ceil(items.length / colNum)
                let i = 0
                for (; i < items.length; i += size) {
                    arr.push(items.slice(i, i+size))
                }
                return arr
            },
            layout__() {
                return 'ncfb-layout-' + this.data.layout
            }
        }
    })

    Ctrl.ALIAS = 'checkbox'

    export default Ctrl
</script>

<style scoped>
    .ncfb-checkboxes {
        display: block;
        vertical-align: middle;
    }
    .ncfb-label {
        position: relative;
        width: 40px;
        height: 20px;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
        transform: translate3d(0,0,0);
        display: inline-block;
        margin-right: 10px;
        vertical-align: middle;
    }
    .ncfb-label:before {
        content: "";
        position: relative;
        top: 3px;
        left: 3px;
        width: 34px;
        height: 14px;
        display: block;
        background: #9a9999;
        border-radius: 8px;
        transition: background .2s ease;
    }
    .ncfb-label span {
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        display: block;
        background: white;
        border-radius: 10px;
        box-shadow: 0 3px 8px rgba(60, 60, 60, .5);
        transition: all .2s ease;
    }
    .ncfb-label span:before {
        content: "";
        position: absolute;
        display: block;
        margin: -18px;
        width: 56px;
        height: 56px;
        background: rgba(31, 18, 86, .5);
        border-radius: 50%;
        transform: scale(0);
        opacity: 1;
        pointer-events: none;
    }
    .ncfb-input:checked + .ncfb-label:before {
        background: #947ADA;
    }
    .ncfb-input:checked + .ncfb-label span {
        background: #4F2EDC;
        transform: translateX(20px);
        transition: all .2s cubic-bezier(.8,.4,.3,1.25), background .15s ease;
        box-shadow: 0 3px 8px rgba(31, 18, 86, .2);
    }
    .ncfb-input:checked + .ncfb-label span:before {
        transform: scale(1);
        opacity: 0;
        transition: all .4s ease;
    }

    .ncfb-checkbox-lbl {
        display: inline-block;
    }

    .ncfb-layout-2-column {
        font-size: 0;
    }
    .ncfb-layout-2-column .ncfb-col {
        display: inline-block;
        width: 50%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-layout-3-column .ncfb-col {
        display: inline-block;
        width: 33%;
        font-size: 16px;
        vertical-align: top;
    }
    .ncfb-layout-side-by-side .ncfb-checkbox {
        display: inline-block;
        margin-right: 20px;
    }
</style>



