<script>
    import Vue from 'vue'

    export default Vue.extend({
        name: 'FbCellTransition',
        data() {
            return {}
        },
        render(h) {
            return h('div', null, this.$slots.default || [])
        },
        updated() {
            const
                me = this,
                $root = me.$root,
                cells = $root.cells,
                insideParent = $root.$options.transitionInsideParent;

            let key, cell

            for (key in cells) {
                cell = cells[key]
                if (cell.moveCb) {
                    cell.moveCb()
                }
            }

            for (key in cells) {
                cell = cells[key]
                cell.newPos = cell.el.getBoundingClientRect()
            }

            for (key in cells) {
                cell = cells[key]

                if (!this.$root.findItem(key).item || !cell.pos) {
                    continue
                }

                const
                    oldPos = cell.pos,
                    newPos = cell.newPos,
                    dx = oldPos.left - newPos.left,
                    dy = oldPos.top - newPos.top,
                    width = oldPos.width - newPos.width,
                    height = oldPos.height - newPos.height,
                    parent = $root.findItem(cell.data.uid).parent;

                // Do not animate eg. elements inside container (there is no point
                // to do this, animating container itself will be enough - children
                // will be moved with parent).
                if (insideParent && (!parent || insideParent != parent.uid)) {
                    continue
                } else if (!insideParent && parent !== null) {
                    continue
                }

                if (dx || dy) {
                    cell.moved = true
                    const s = cell.el.style
                    s.transform = `translate(${dx}px, ${dy}px)`
                    // s.width = oldPos.width + 'px'
                    // s.height = oldPos.height + 'px'
                    s.transitionDuration = '0s'
                }
            }

            // force reflow to put everything in position
            // assign to this to avoid being removed in tree-shaking
            // $flow-disable-line
            this._reflow = document.body.offsetHeight

            for (key in cells) {
                cell = cells[key]
                if (!this.$root.findItem(key).item) {
                    continue
                }
                if (cell.moved) {
                    const
                        el = cell.el,
                        s = el.style;

                    el.classList.add('cells-move')
                    s.transform = s.transitionDuration = ''
                    s.width = cell.pos.width
                    el.addEventListener('transitionend', cell.moveCb = (e) => {
                        if (e && e.target !== el) {
                            return
                        }
                        if (!e || /transform$/.test(e.propertyName)) {
                            el.removeEventListener('transitionend', cell.moveCb)
                            cell.moveCb = null
                            el.classList.remove('cells-move')
                        }
                    })
                }
            }
        }
    })
</script>


