<template>
    <div class="ncfb-select ncfb-ctrl" :class="{ 'ncfb-error-ctrl': error }">
        <component :is="cmpLbl" :label="data.label" :help="data.help"></component>
        <select class="ncfb-input"
                ref="input"
                :class="{ 'ncfb-input-error': error }">
            <option v-for="item in data.values" :key="item.uid" :value="item.value" :selected="item.uid == data.selected">{{ item.value }}</option>
        </select>
        <span v-if="data.help && $root.form.lblPos === 'left'" class="ncfb-help">{{ data.help }}</span>
        <span class="ncfb-error" v-show="error">{{ error }}</span>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../../renderer/AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-select',
        methods: {
            getValue() {
                return this.$refs.input.value
            }
        }
    })

    Ctrl.ALIAS = 'select'

    export default Ctrl
</script>



