<template>
    <p class="ncfb-paragraph" v-bind:style="{ textAlign: data.align }" v-text="data.value"></p>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        name: 'fb-paragraph',
        methods: {
            value() {
                return null
            }
        }
    })

    Ctrl.ALIAS = 'paragraph'

    export default Ctrl
</script>



