<template>
    <div class="ncfb-body">
        <h2 class="ncfb-value" :style="{ textAlign: data.align }" @click="$root.openProps(data.uid, { tab: 'basic', focus: 'value' })">
            <span v-text="data.value"></span>
            <ui-icon name="edit" color="#999" width="16" style="margin-bottom: -3px;"></ui-icon>
        </h2>
    </div>
</template>
<script>
    import Vue from 'vue'
    import AbstractControl from './../AbstractControl'
    import UiIcon from './../../ui/Icon'
    import { extend, clone } from './../../../util/util'

    const Ctrl = Vue.extend({
        extends: AbstractControl,
        components: { UiIcon },
        name: 'fb-heading'
        // watch: {
        //     // When "tag" changes, update control subtype.
        //     'data.tag'(val) {
        //         switch (val) {
        //             case 'h1':
        //                 val = 'heading'
        //                 break
        //             case 'h2':
        //                 val = 'heading2'
        //                 break
        //             case 'h3':
        //                 val = 'heading3'
        //                 break
        //             case 'h4':
        //                 val = 'heading4'
        //                 break
        //         }
        //
        //         this.$root.updateCtrl(this.data.uid, 'type', this.data.type.split('.')[0] + '.' + val)
        //     }
        // }
    })

    Ctrl.ALIAS = 'heading'
    const data = {
        align: 'left',
        value: 'Hello world!',
    }

    const validators = {}

    const info = {
        internal: false,
        desc: '',
        icon: '<svg style="width: 40px" xmlns="http://www.w3.org/2000/svg" fill-rule="evenodd" stroke-linejoin="round" stroke-miterlimit="1.4" clip-rule="evenodd" viewBox="0 0 18 6"><text x="-.1" y="5.6" fill="#6e6e6e" font-family="\'TimesNewRomanPSMT\',\'Times New Roman\',serif" font-size="8">Hello</text></svg>'
    }

    Ctrl.info = {
        root: extend(clone(info), {
            name: 'Heading',
            group: 'text'
        }),
        heading: extend(clone(info), {
            name: 'Heading 1',
            data: clone(data),
            validators: validators
        }),
        heading2: extend(clone(info), {
            name: 'Heading 2',

            data: clone(data),
            validators: validators
        }),
        heading3: extend(clone(info), {
            name: 'Heading 3',
            data: clone(data),
            validators: validators
        }),
        heading4: extend(clone(info), {
            name: 'Heading 4',
            data: clone(data),
            validators: validators
        })
    }

    export default Ctrl
</script>
<style scoped>
    .ncfb-value {
        margin: 10px;
        cursor: pointer;
    }
    .ncfb-value span {
        font-size: 18px;
    }
</style>



