<template>
    <a @click="$emit('click', $event)" class="ncfb-btn" :class="'ncfb-' + type__"><ui-icon :name="icon__" v-if="icon__"></ui-icon> {{ text }}</a>
</template>

<script>
    import UiIcon from './Icon'

    export default {
        name: 'ui-btn',
        components: { UiIcon },
        props: {
            /**
             * Available button types:
             * - add - green add
             * - list-up - blue move up
             * - list-down - blue move down
             * - list-rm - red delete button
             * - reset - grey reset button
             */
            type: {
                type: String,
                required: true
            },
            text: {
                type: String,
                default: ''
            }
        },
        computed: {
            type__() {
                switch (this.type) {
                    case 'add':
                        return 'add'
                    case 'add-sm':
                        return 'add-sm'
                    case 'reset':
                        return 'reset'
                    case 'list-up':
                    case 'list-down':
                    case 'list-rm':
                        return 'list-btn ncfb-' + this.type
                }
            },
            icon__() {
                switch (this.type) {
                    case 'add':
                        return 'plus'
                    case 'add-sm':
                        return false
                    case 'reset':
                        return false
                    case 'list-up':
                    case 'list-down':
                        return 'arrow-down'
                    case 'list-rm':
                        return 'close'
                }
            }
        }
    }
</script>

<style scoped>
    /* COMMON BUTTON STYLES */
    /* Watch out what you put in here! */
    .ncfb-btn {
        font-size: 14px;
        border-radius: 6px;
        user-select: none;
        text-align: center;
        cursor: pointer;
        transition: all 250ms ease-out;
        display: inline-flex;
        align-items: center;
        justify-content: center;
    }
    .ncfb-btn >>> .ncfb-icon {
        vertical-align: middle;
    }

    /* ADD BUTTON */
    .ncfb-add {
        padding: 10px 10px;
        height: 43px;
        min-width: 100px;
        max-width: 200px;
        background: #8bc34a;
        color: #fff;
    }
    .ncfb-add .ncfb-icon {
        height: 20px;
        margin-right: 5px;
    }
    .ncfb-add:hover {
        background: #9edb4f;
    }
    .ncfb-add >>> path {
        fill: #fff !important;
    }

    /* RESET BUTTON */
    .ncfb-reset {
        padding: 10px 10px;
        height: 43px;
        min-width: 100px;
        max-width: 200px;
        background: #808080;
        color: #fff;
    }
    .ncfb-reset:hover {
        background: #989898;
    }

    /* SMALL ADD BUTTON */
    .ncfb-add-sm {
        background: #8bc34a;
        color: #fff;
        padding: 6px 10px;
    }
    .ncfb-add-sm:hover {
        background: #9edb4f;
    }

    /* LIST BUTTON */
    .ncfb-list-btn {
        vertical-align: middle;
        background: #88a7f1;
        padding: 3px;
    }
    .ncfb-list-btn:hover {
        background: #afc6ff;
    }
    .ncfb-list-up .ncfb-icon {
        transform: rotate(180deg);
    }

    .ncfb-list-rm {
        background: #FF6737;
    }
    .ncfb-list-rm:hover {
        background: #eb4d2e;
    }
    .ncfb-list-rm >>> path {
        fill: #fff;
    }
</style>



