<script>
    import $ from 'cash-dom'
    import { get } from 'evforms/util/util'

    export default {
        props: {
            // Control data.
            data: {
                type: Object,
                required: true
            }
        },
        data() {
            return {
                // Currently active tab.
                tab: 'basic'
            }
        },
        methods: {
            /**
             * Validates data provided to input and if valid updates control.
             */
            vchange(e) {
                const target = $(e.target),
                    key = target.attr('prop'),
                    // ui-checkbox event have "value" key.
                    value = e.value ? e.value : target.val();

                if (target.next().hasClass('ncfb-of-e')) {
                    target.next().remove()
                }

                const errors = this.$root.updateCtrl(this.data.uid, key, value)

                if (errors.length) {
                    e.preventDefault()

                    if (!target.next().hasClass('ncfb-of-e')) {
                        target.parent().append('<span class="ncfb-of-e"></span>')
                    }
                    target.next().text(errors[0])
                    return
                }
            },
            /**
             * Sets default value if value is empty or not valid.
             * @param e
             */
            setDefault(e) {
                const
                    me = this,
                    target = $(e.target),
                    key = target.attr('prop'),
                    error = target.next().hasClass('ncfb-of-e');

                if (target.val() == '' || error) {
                    const value = get(key, me.$root.getCtrlDef(me.data.type).info.data)
                    target.val(value)

                    if (error) {
                        target.next().remove()
                    }
                }
            },
        },
        watch: {
            // When data is changed, reset active tab to 'basic'.
            data() {
                this.tab = 'basic'
            }
        }
    }
</script>



